  /* 02 - export MoreData class */
  export class MoreData {
    constructor($moredata, classtoapply) {
      this.$moredata = $moredata;
      this.classtoapply = classtoapply;
      this.$viewMoreButton = this.$moredata.find('.view-more-btn-v');
      this.$viewLessButton = this.$moredata.find('.view-less-btn-v');
      this.$moreinitialContent = this.$moredata.find(this.classtoapply +"__summary");
      this.$morefullContent = this.$moredata.find(this.classtoapply +"__fulldetails");
      this.$initialContent = this.$moreinitialContent.text();
      this.initialEllipseText = this.buildEllipseText(this.$initialContent);
    }

    init() {
      this.showPartData();
      this.addEventListeners();
    }

    addEventListeners() {

        this.$viewMoreButton
        .on('click', e => {
          e.preventDefault();
          this.$viewMoreButton.addClass('hidden');
          this.$viewLessButton.removeClass('hidden');
          this.showFullData();
        })

        this.$viewLessButton
        .on('click', e => {
          e.preventDefault();
          this.$viewLessButton.addClass('hidden');
          this.$viewMoreButton.removeClass('hidden');
          this.showPartData();
        })
    }

    showPartData(){
        if(this.initialEllipseText.length < this.$initialContent.length){
           this.$moreinitialContent.text(this.initialEllipseText);
           this.$moreinitialContent.show();
           this.$moreinitialContent.slideDown('slow');
           this.$morefullContent.slideUp('slow');
           this.$morefullContent.hide();
         }else{
           this.$viewMoreButton.addClass('hidden');
        }
    }

    showFullData(){
        this.$moreinitialContent.slideDown('slow');
        this.$moreinitialContent.hide();
        this.$morefullContent.show();
        //this.$morefullContent.slideDown('slow');
    }

    buildEllipseText(content){

      this.lineHeight = parseFloat(this.$moreinitialContent.css('line-height'));
      this.maxHeight = 3 * this.lineHeight;

      let newContent = "", prevContent = "";
      const contentArr = content.toString().split('');
      this.$moreinitialContent.html("");

      for(let h=0; h< contentArr.length; h++){

        prevContent = newContent;
        newContent = newContent + contentArr[h];
        this.$moreinitialContent.html(newContent);

        if(this.$moreinitialContent.outerHeight() > this.maxHeight){
          newContent = prevContent.substring(0, prevContent.length - 3).trim() + "...";
          return newContent;
        }
      }

      return content;
    }


  }
