
/* ==============================================================================
Index
----------------------------------------
01 - imports
02 - init method - declare props && trigger load script
03 - append script, trigger ajax call
04 - ajax call, handle data when retrieved
05 - filter data with initial options, generate cards & trigger setMapCenter
06 - use selected city/location to retrieve geocode, if !map loadMap
07 - instance google map, create cards & autocomplete when loaded
08 -
09 -
============================================================================== */
/* 01 - imports */
import {
  SingleBranch
} from './c-branch-finder-branch.js';
import { dimmerMap } from './helpers.js';


export class BranchFinder {

  constructor($branchFinder) {
    this.$branchFinder = $branchFinder;
    this.isUserLocation = false;
    this.$detailsContainer = $('.c-branch-container');
    this.$tabs = new LocationTabs($('.c-branch-list__tabs-list'), this);
    this.$dropdown = new LocationDropdown($('.c-branch-dropdown'), this);
    this.mobileNav = new LocationMobileNav(this);
    this.enableLocEn = "Please allow browser's location services to get Current Location.";
    this.enableLocAr = "يرجى السماح لخدمات موقع المتصفح للحصول على الموقع الحالي.";
    this.disabledLocationMsgEn = `You have disabled location services. Enter Browser Settings to allow location services.`;
    this.disabledLocationMsgAr= "لقد قمت بتعطيل خدمات الموقع. أدخل إعدادات المتصفح للسماح بخدمات الموقع.";
  }

  /* 02 - init method - declare props && trigger load script */
  init() {
    this.declareProps();
    this.loadMapScript()
  }

  declareProps() {
    this.setApiProps();
    this.initDefaultValues();
    this.branchesJson = this.$branchFinder.attr('data-json');
    this.branchList;
    this.visibleBranches = [];
    this.branchCities = {};
    this.mapMarkers = [];
    this.userMarker = [];
    this.infoWindows = [];
    this.clickedMarkers = {};
  }

  initDefaultValues() {
    this.$tabs.$tabs.each((i, tab) => {
      //console.log($(tab).data('branchtype'));
      if ($(tab).hasClass('active')) this.selectedType = $(tab).data('branchtype');
    })
    this.selectedCity = this.$branchFinder.attr('data-city');
  }

  setApiProps() {
    this.apiKey = this.$branchFinder.attr('data-key');
    this.gmapSrc = `https://maps.googleapis.com/maps/api/js?key=${this.apiKey}&libraries=places`;
  }

  /* 03 - append script, trigger ajax call  */
  loadMapScript() {
    const script = document.createElement('script');
    script.type = 'text/javascript';
    script.defer = true;
    document.getElementsByTagName('head')[0].appendChild(script);
    script.onload = () => this.branchList = this.getBranches();    
    script.src = this.gmapSrc;
  }

  /* 04 - ajax call, handle data when retrieved  */
  getBranches() {
    $.ajax({
      method: 'GET',
      url: this.branchesJson
    }).done(data => {
      try {
        this.handleData(data);
      } catch (error) {
        //console.log(error);
      }
    });
  }

  /* 05 - filter data with initial options, generate cards & trigger setMapCenter  */
  handleData(data) {
    this.branchList = data;
    this.visibleBranches = this.filterUndefined(this.branchList);
    this.visibleBranches = this.filterByType(this.visibleBranches);
    this.visibleBranches = this.filterByCity(this.visibleBranches);
    this.generateHtml();
    this.setMapCenter();
  }

  getGeoLocationError(code) {
    switch (code) {
      case 1:
        return 'Permission Denied';
      case 2:
        return 'Position unavailable';
      case 3:
        return 'Timeout reached';
    }
  }

  getGeoLocation(isAddress) {
    if (navigator.permissions) {
      navigator.permissions.query({name: 'geolocation'}).then(permission => {
        if (permission.state === "granted" || permission.state === "prompt") {
          if (navigator.geolocation && !this.userPos && !isAddress) {
            navigator.geolocation.getCurrentPosition(pos => {
              this.userPos = {
                lat: pos.coords.latitude,
                lng: pos.coords.longitude
              }
              dimmerMap(false);
              this.map.setCenter(this.userPos);
              this.map.setZoom(13)
              this.calculateDistances();
            },
            err => {
              //alert(`Error (${err.code}): ${this.getGeoLocationError(err.code)}`);
              let disabledLocationMsg = this.disabledLocationMsgEn;
              if (document.getElementsByTagName("html")[0].getAttribute("lang") == "ar") {
                disabledLocationMsg = this.disabledLocationMsgAr;
              }
              alert(disabledLocationMsg);
              dimmerMap(false);
            })
          }
          else if(this.userPos) {
            dimmerMap(false);
            this.map.setCenter(this.userPos);
            this.map.setZoom(13)
            this.calculateDistances(isAddress);
          }
        }
        else {
          dimmerMap(false);
          //this.$dropdown.resetDropdown();
          let enableLocationMsg = this.enableLocEn;
          if (document.getElementsByTagName("html")[0].getAttribute("lang") == "ar") {
            enableLocationMsg = this.enableLocAr;
          }
          alert(enableLocationMsg);
        }
      })
    } 
  }

  displayUserPos(userPos) {
    this.resetUserMarker();
    const iconUrl = './../assets/toolkit/images/branch-finder/map-user.png';
    const userMarker = new google.maps.Marker({
      position: this.userPos,
      map: this.map,
      zIndex: 999,
      icon: {
        url: iconUrl,
        scaledSize: new google.maps.Size(50, 50),
        origin: new google.maps.Point(0, 0),
        anchor: new google.maps.Point(0, 0)
      }
    });
    this.userMarker.push(userMarker)
    return userMarker;
  }

  calculateDistances(isAddress) {
    let branchArray = Array.from(this.branchList); // deep clone
    branchArray = this.filterUndefined(branchArray);
    branchArray
      .forEach(branch => {
        this.setBranchCities(branch.city);
        const
          branchLat = Number(branch.latitude),
          branchLng = Number(branch.longitude),
          userLat = isAddress ? this.userPos.lat() : this.userPos.lat,
          userLng = isAddress ? this.userPos.lng() : this.userPos.lng;

        branch['distance'] = this.calcDist(userLat, userLng, branchLat, branchLng);
      });
    this.visibleBranches = this.filterByDistance(branchArray);
    this.visibleBranches = this.filterByType(this.visibleBranches);
    this.generateHtml();
    this.displayUserPos(this.userPos);
  }

  handleFilter() {
    this.visibleBranches = this.filterUndefined(this.branchList);
    this.visibleBranches = this.filterByCity(this.visibleBranches);
    this.visibleBranches = this.filterByType(this.visibleBranches);
    this.setMapCenter();
    this.generateHtml();
    this.resetUserMarker();
    this.isUserLocation = false;
  }

  resetUserMarker() {
    if (this.userMarker.length) {
      this.userMarker.forEach(marker => marker.setMap(null))
    }
    this.userMarker = [];
  }

  handleDistanceFilter(isAddress = false) {
    dimmerMap(true);
    this.getGeoLocation(isAddress);
    this.isUserLocation = true;
  }

  filterByDistance(branchArray) {
    const branches = Array.from(this.branchList); //deep clone - original list isn't affected
    return branchArray.sort((prev, curr) => {
      return prev.distance - curr.distance;
    })
  }

  filterByCity(branchArray) {
    return branchArray.filter(branch => {
      this.setBranchCities(branch.city);
      //console.log(branch, branch.city);
      //console.log(this.selectedCity)
      return branch.city.toLowerCase() === this.selectedCity.toLowerCase();
    })
  }

  filterByType(branchArray) {
    return branchArray.filter(branch => {
      return branch.branchtype.toLowerCase() === this.selectedType.toLowerCase();
    });
  }

  filterUndefined(branchArray) {
    return branchArray.filter(branch => {
      return !!branch.latitude && !!branch.longitude && !!branch.branchtype
    })
  }

  resetMarkers() {
    this.mapMarkers.forEach(marker => {
      marker.setMap(null);
    })
    this.mapMarkers = [];
  }

  generateHtml() {
    if(!!this.isLoaded) {
      google.maps.event.removeListener(this.isLoaded);
    }
    this.$detailsContainer.empty();
    this.resetMarkers();
    this.visibleBranches.forEach((branch, i) => {
      const
        branchId = `#branch-${i}`,
        htmlBranch = new SingleBranch(branch, branchId),
        branchMarker = this.generateMarker(branch);

      this.initSingleBranch(htmlBranch);
      this.initMarkerListeners(branch, branchMarker, htmlBranch, $(branchId), branchId);
      this.initCardListeners(branch, branchMarker, $(branchId));
      this.mapMarkers.push(branchMarker);
    })
  }

  initCardListeners(branch, marker, $template) {
    const directionsLink = $template.find('.c-branch-item__link');

    directionsLink.on('click', e => {
      this.openDirections(branch)
    })
    $template.on('click', e => {
      this.openInfoWindow(branch, marker);
      this.resetMarkerStyles();
      marker.setIcon(this.getIcon(true));
    })
  }

  openDirections(branch) {
    window.open(this.getDirections(branch), '_blank')
  }

  getDirections(branch) {
    return `https://www.google.com/maps/dir/?api=1&destination=${Number(branch.latitude)},${Number(branch.longitude)}`;
  }

  initMarkerListeners(branch, marker, cardClass, $template, id) {
    this.clickedMarkers[id] = false;
    const hoverIcon =
    google.maps.event.addListener(marker, 'click', (e) => {
      this.openInfoWindow(branch, marker);
      cardClass.toggleClass($template);
      this.scrollToCard($template);
    })

    google.maps.event.addListener(marker, 'mouseover', e => {
      marker.setIcon(this.getIcon(true));
      //console.log(this.clickedMarkers)
    })

    google.maps.event.addListener(marker, 'click', e => {
      this.resetMarkerStyles();
      marker.setIcon(this.getIcon(true));
      this.clickedMarkers[id] = true;
    })

    google.maps.event.addListener(marker, 'mouseout', e => {
      if(this.clickedMarkers[id] === false)
      marker.setIcon(this.getIcon());
    })
  }

  resetMarkerStyles() {
    Object.keys(this.clickedMarkers).forEach(marker => this.clickedMarkers[marker] = false);
    this.mapMarkers.forEach(marker => marker.setIcon(this.getIcon()))
  }

  scrollToCard($template) {
    const scrollPx = $template.offset().top + this.$detailsContainer.scrollTop() - $('.c-branch-list__wrapper').outerHeight() - $(document).scrollTop();
    this.$detailsContainer.animate({ scrollTop: scrollPx }, { duration: 'medium', easing: 'swing' });
  }

  openInfoWindow(branch, marker) {
    const info = this.addInfoWindow(branch);

    this.resetInfoWindows();
    info.open(this.map, marker);
    this.infoWindows.push(info);
  }

  resetInfoWindows() {
    if (this.infoWindows.length) {
      this.infoWindows.forEach(infoWindow => {
        infoWindow.close();
      })
    }
    this.infoWindows = [];
  }

  addInfoWindow(branch) {
    const infoWindow = new InfoWindow(branch, this.getDirections(branch))
    return new google.maps.InfoWindow({
      content: infoWindow.getContent()
    })
  }

  initSingleBranch(branch) {
    this.$detailsContainer.append(branch.htmlTemplate);
    branch.init();
  }

  getIcon(isHover = false) {
    const iconUrl = isHover ? './../assets/toolkit/images/branch-finder/map-marker-hover.png' : './../assets/toolkit/images/branch-finder/map-marker.png';

    return {
      url: iconUrl,
      scaledSize: new google.maps.Size(55.55, 66.66),
      origin: new google.maps.Point(0, 0),
      anchor: new google.maps.Point(0, 0)
    };
  }

  generateMarker(branch, user = false) {
    return new google.maps.Marker({
      position: this.getBranchPos(branch),
      map: this.map,
      icon: this.getIcon()
    })
  }

  getBranchPos(branch) {
    return {
      lat: Number(branch.latitude),
      lng: Number(branch.longitude)
    }
  }

  setBranchCities(city)  {
    if (!this.branchCities[city]) {
      this.branchCities[city] = city;
      this.$dropdown.addDropdownOpt(city);
    }
  }

  getMapPos(lat, lng) {
    return new google.maps.LatLng(lat, lng);
  }

  /* 06 - use selected city/location to retrieve geocode, if !map loadMap  */
  setMapCenter() {
    const geocoder = new google.maps.Geocoder();

    geocoder.geocode({
      'address': this.selectedCity
    }, (results, status) => {
      if (status === google.maps.GeocoderStatus.OK) {
        const
          resultLocation = results[0].geometry.location,
          lat = resultLocation.lat(),
          lng = resultLocation.lng(),
          mapCenter = this.getMapPos(lat, lng);

        this.initialPos = mapCenter;
        if (!this.map) {
          this.loadMap();
        } else {
          this.map.setCenter(this.initialPos);
          this.map.setZoom(10);
          this.mapBounds = this.map.getBounds();
        }
      }
    })
  }

  /* 07 - instance google map, create cards & autocomplete when loaded  */
  loadMap() {
    this.map = new google.maps.Map(document.getElementById('gmap'), {
      zoom: 10,
      center: this.initialPos,
      fullscreenControl: false,
      streetViewControl: false,
      mapTypeControl: false
    });
    this.isLoaded = this.map.addListener('tilesloaded', () => {
      this.generateHtml();
      this.loadAutocomplete();
      dimmerMap(false, true)
    });
  }

  loadAutocomplete() {
    this.$countryAutocomplete = new LocationAutocomplete($('#country-autocomplete'), this, true);
    this.$cityAutocomplete = new LocationAutocomplete($('#city-autocomplete'), this, false);
  }

  calcDist(lat1, lng1, lat2, lng2) {
    const
      p = 0.017453292519943295,
      c = Math.cos,
      a = 0.5 - c((lat2 - lat1) * p) / 2 +
      c(lat1 * p) * c(lat2 * p) *
      (1 - c((lng2 - lng1) * p)) / 2;

    return 12742 * Math.asin(Math.sqrt(a)); // 2 * R; R = 6371 km
  }
}

class LocationMobileNav {
  constructor(branchFinder) {
    this.branchFinder = branchFinder;
    this.$branchFinder = $('.c-branch-finder');
    this.init();
  }

  init() {
    this.handleViews()
  }

  handleViews() {
    const $navButtons = this.$branchFinder.find('.c-branch-finder__nav-mobile a');
    if ($navButtons.length) {
      $navButtons.each((i, $button) => {
        $button = $($button);
        const isMap = $button.attr('data-viewtype') === 'map';
        $button
          .on('click', e => {
            e.preventDefault();
            this.toggleMobileViews(isMap, $button);
          })
      })
    }
  }

  resetNavStyles() {
    this.$branchFinder.find('.c-branch-finder__nav-mobile a').each((i, navLink) => {
      $(navLink).removeClass('active');
    })
  }

  toggleMobileViews(isMap, $link) {
    const isList = $link.html().toLowerCase().includes('list');
    this.resetNavStyles();
    $link.addClass('active');
    if (isList) {
      this.branchFinder.$detailsContainer.addClass('show');
    } else {
      this.branchFinder.$detailsContainer.removeClass('show');
    }
  }
}

class LocationTabs {
  constructor($tabContainer, branchFinder) {
    this.$tabContainer = $tabContainer;
    this.$tabs = this.$tabContainer.find('a');
    this.branchFinder = branchFinder;
    this.init();
  }
  init() {
    this.addEventListeners();
  }
  addEventListeners() {
    this.$tabs.each((i, tab) => {
      $(tab)
        .on('click', e => {
          e.preventDefault();
          this.toggleTabs($(tab));
        })
    })
  }

  resetTabs() {
    this.$tabs.each((i, tab) => {
      if ($(tab).hasClass('active')) $(tab).removeClass('active');
    })
  }

  toggleTabs($tab)  {
    this.resetTabs();
    $tab.addClass('active');
    //this.branchFinder.selectedType = $tab.html();
    this.branchFinder.selectedType = $tab.data('branchtype');
    if (this.branchFinder.isUserLocation) {
      this.branchFinder.handleDistanceFilter(this.branchFinder.isAddress);
    } else {
      this.branchFinder.handleFilter();
    }
  }
}

class LocationDropdown {
  constructor($dropdown, branchFinder) {
    this.branchFinder = branchFinder;
    this.$dropdown = $dropdown;
    this.$placeholder = this.$dropdown.find('.c-branch-dropdown__placeholder');
    this.placeholderText = this.$placeholder.attr('data-placeholder');
    this.$content = this.$dropdown.find('.c-branch-dropdown__content');
    this.currLocation = this.$content.attr('data-locale');
    this.open = false;
    this.init();
  }

  init() {
    this.addEventListeners();
    this.addDropdownOpt(this.currLocation);
  }

  addEventListeners() {
    this.$dropdown.on('click', e => {
      e.stopPropagation();
      this.toggleDropdown();
    })
    $(document)
      .on('click', e => {
        if (this.open) this.toggleDropdown();
      })
  }

  handleOptions() {
    const $options = this.$content.find('.c-branch-dropdown__option');
    $options.each((i, option) => {
      $(option)
        .off('click')
        .on('click', e => {
          e.stopPropagation();
          this.handleSelect($(option));
        })
    })
  }

  handleSelect($option) {
    const
      city = $option.html(),
      isCity = city !== this.currLocation,
      $countryAuto = $('#country-autocomplete'),
      $cityAuto = $('#city-autocomplete');
    this.branchFinder.selectedCity = city;
    this.branchFinder.isAddress = false;
    this.$placeholder.html(city);
    if (isCity) {
      $cityAuto.css('display','block');
      $countryAuto.css('display', 'none');
      $countryAuto.val('')
      this.branchFinder.handleFilter();
    } else {
      $cityAuto.css('display', 'none');
      $countryAuto.css('display', 'block');
      $cityAuto.val('')
      this.branchFinder.userPos = '';
      this.branchFinder.handleDistanceFilter();
    }

    this.toggleDropdown();
  }

  resetDropdown() {
    this.$placeholder.html(this.placeholderText);
    this.branchFinder.map.set
  }

  toggleDropdown() {
    if (this.open) {
      this.$dropdown.removeClass('is-open');
    } else {
      this.$dropdown.addClass('is-open');
    }
    this.open = !this.open;
  }

  addDropdownOpt(city) {
    const isCity = city !== this.currLocation;
    this.$content.append(`<span class='c-branch-dropdown__option'>${city}</span>`);
    if (!this.$placeholder.html() && isCity) {
      this.$placeholder.html(this.branchFinder.selectedCity);
    }
    this.handleOptions();
  }
}

class LocationAutocomplete {
  constructor($autocomplete, branchFinder, isCountry) {
    this.$autocomplete = $autocomplete;
    this.autocomplete = $autocomplete[0];
    this.isCountry = isCountry;
    this.branchFinder = branchFinder;
    this.init();
  }
  init() {
    const
      htmlInput = this.autocomplete,
      options = {
        strictBounds: true,
      };
    this.autocomplete = new google.maps.places.Autocomplete(htmlInput, options);
    this.autocomplete.setOptions(options);
    this.evalDropdown();
    this.addEventListeners();
  }


  evalDropdown() {
    if (!this.isCountry) {
      this.bindToMap()
    } else {
      this.autocomplete.setOptions({
        componentRestrictions: { 'country': 'ae' }
      })
    };
  }

  addEventListeners() {
    this.autocomplete.addListener('place_changed', () => this.handlePlaceChanged());

    this.$autocomplete
      .on('focus', e => {
        this.handleFocus();
      })
  }

  bindToMap() {
    this.autocomplete.bindTo('bounds', this.branchFinder.map);
  }

  handleFocus() {
    if(this.branchFinder.isUserLocation) {
      this.branchFinder.$dropdown.resetDropdown();
    }
  }

  handlePlaceChanged() {
    const place = this.autocomplete.getPlace().geometry.location;
    this.branchFinder.userPos = place;
    this.branchFinder.isAddress = true;
    this.branchFinder.handleDistanceFilter(true);
  }
}

class InfoWindow {
  constructor(branch, link) {
    this.branch = branch;
    this.link = link;
  }

  getContent() {
    const {
      branch,
      location,
      pobox,
      facilities
    } = this.branch;
    return (`
      <div class='c-branch-item__info-window'>
        <h6>
          ${branch}
        </h6>
        <p>
        ${location} ${pobox}
        </p>
        <a href=${this.link} target=”_blank” class="c-branch-item__link">${window.ADCB.mapTranslations.getdirection}</a>
      </div>
    `);
  }
}
