export class Pagination {
    constructor($form) {
        this.$form = $form;
        this.prefix = this.$form[0].getAttribute('data-prefix');
    }
    init(){
        this.addEventListeners();
    }
    addEventListeners() {
        this.$form.on('change', e => {
            e.target.value = this.beautifyValue(e.target.value);
        })
    }

    beautifyValue(value){
        return `${this.prefix} ${value}`;
    }
}