/* 02 - export MegaTabs class */
export class MegaTabs {
  
  constructor($overallContainer) {
    this.$overallContainer = $overallContainer;
    this.$smallTabSelectLink = this.$overallContainer.find('.js-mega-tabs__selected-link');
    this.$smallTabNavLinks = this.$overallContainer.find('.c-input-select__option');
    this.$smallTabNavDropdown = this.$overallContainer.find('.c-input-select__dropdown');
    this.$selectInputOptions = this.$overallContainer.find('.select-input option');
    this.$largeTabContainer = this.$overallContainer.find('.c-tab-nav__list');
    this.$largeTabListItems = this.$overallContainer.find('.c-tab-nav__item');
    this.$largeTabNavLinks = this.$largeTabContainer.find('.c-tab-nav__link');    
  }

  init() {
    this.setDataIds();
    this.setSmallActiveTab();
    this.addEventListeners();
    this.updateButtonText();
  }
  setDataIds() {
    for (let i = 0; i < this.$selectInputOptions.length; i++)
    {
      $(this.$smallTabNavLinks[i]).attr('data-xs-id', $(this.$selectInputOptions[i]).attr('data-xs-id'));
    }
  }
  addEventListeners() {
    this.$largeTabNavLinks.on('click', e => {
      this.$largeTabContainer.find('.is-active').removeClass('is-active');
      //console.log($(e));
      $(e.currentTarget).addClass('is-active');
      this.setSmallActiveTab();
      this.updateButtonText();
    });
    this.$smallTabNavLinks.on('click', e => {
      this.$smallTabNavDropdown.find('.is-active').removeClass('is-active');
      $(e.target).addClass('is-active');
      this.setLargeActiveTab();
      this.updateButtonText();
    });
  }
  setLargeActiveTab() {
    let dataXsID = this.getSmallActiveTab().attr('data-xs-id');
    this.$largeTabNavLinks.removeClass('is-active');
    this.$largeTabListItems.filter("[data-lg-id='"+ dataXsID +"']").children('a').addClass('is-active');
  }
  setSmallActiveTab() {
    let dataLgID = this.getLargeActiveTab().attr('data-lg-id');
    this.$smallTabNavLinks.removeClass('is-active');
    this.$smallTabNavLinks.filter("[data-xs-id='"+ dataLgID +"']").addClass('is-active');
  }
  getLargeActiveTab() {
    return this.$largeTabNavLinks.filter('.is-active').parent();
  }
  getSmallActiveTab() {
    return this.$smallTabNavLinks.filter('.is-active');
  }
  updateButtonText() {
    this.$smallTabSelectLink.val(this.getSmallActiveTab().text());
  }
}
