export class BasicCalculator {
  add(...args) {
    let result = 0;
    args.forEach(element => {
      result = element + result;
    });
    return result;
  }

  subtract(...args) {
    let result = 0;
    if (args.length > 1) {
      result = args[0];
      args.slice(1).forEach(element => {
        result = result - element;
      });
    } else if (args)
      return -args[0];

    return result;
  }

  multiply(...args) {
    let result = 1;
    args.forEach(element => {
      result = result * element;
    });
    return result;
  }
  divide(...args) {
    let result = 0;
    if (args.length > 1) {
      result = args[0]
      args.slice(1).forEach(element => {
        if (element !== 0) {
          result = result / element;
        }
        else {
          result = 0;
        }
      });
    } else if (args)
      return (args[0])
    return result;
  }

  mod(...args) {
    let result = 0;
    args.forEach((element, index) => {
      if (index == 0) {
        result = element;
      } else {
        result = result % element;
      }
    });
    return result;
  }

  pow(...args) {
    let result = 0;
    if (args.length > 1) {
      result = args[0]
      args.slice(1).forEach(element => {
        result = Math.pow(result, element);
      });
    } else if (args) {
      return args[0]
    }
    return result;
  }

  reducedBalanceInterest(rate, paymentPeriod, presentValue, futureValue = 0, paymentsDue = 0) {
    const
      percent = rate / 100,
      monthlyRate = percent / 12;
    return (
      (this.multiply((this.multiply(presentValue * monthlyRate)), this.pow(this.add(1, monthlyRate), paymentPeriod))) /
      (this.subtract(this.pow(this.add(1, monthlyRate), paymentPeriod), 1))
    )
  }

  checkNumeric(value) {
    return /^\d*\.?\d*$/.test(value);
  }

  setInputMask($input, inputFilter) {
    ["input", "keydown", "keyup", "mousedown", "mouseup", "select", "contextmenu", "drop"].forEach(function (event) {
      $input.on(event, function () {
        if (inputFilter(this.value)) {
          this.oldValue = this.value;
          this.oldSelectionStart = this.selectionStart;
          this.oldSelectionEnd = this.selectionEnd;
        } else if (this.hasOwnProperty("oldValue")) {
          this.value = this.oldValue;
          this.setSelectionRange(this.oldSelectionStart, this.oldSelectionEnd);
        }
      });
    });
  }

  displayNumber(result, $target) {
    try {
      $target.html(result);
    } catch (error) {
      $target.innerText = result;
    }
  }

  addCommas (value) {
    let size, decimals;
    let minus = false;

    value = value.toString();
    if (value.includes("-")) {
      value = value.replace("-", "")
      minus = true;
    }
    if (value.includes(".")) {
      decimals = (value + "").split(".");
      size = decimals[0].length - 3;
      while (size > 0) {
        decimals[0] = [decimals[0].slice(0, size), ',', decimals[0].slice(size)].join('');
        size = size - 3;
      }
      if (minus)
        value = "-" + value;
    } else {
      size = value.length - 3;
      while (size > 0) {
        value = [value.slice(0, size), ',', value.slice(size)].join('');
        size = size - 3;
      }
      if (minus)
        value = "-" + value;
    }
    return value;
  }
  setMillionsMask(value, $target) {
    let size, decimals;
    let minus = false;

    value = value.toString();
    if (value.includes("-")) {
      value = value.replace("-", "")
      minus = true;
    }
    if (value.includes(".")) {
      decimals = (value + "").split(".");
      size = decimals[0].length - 3;
      while (size > 0) {
        decimals[0] = [decimals[0].slice(0, size), ',', decimals[0].slice(size)].join('');
        size = size - 3;
      }
      if (minus)
        value = "-" + value;
      this.displayNumber(decimals[0] + "." + decimals[1], $target);
    } else {
      size = value.length - 3;
      while (size > 0) {
        value = [value.slice(0, size), ',', value.slice(size)].join('');
        size = size - 3;
      }
      if (minus)
        value = "-" + value;
      this.displayNumber(value, $target);
    }
  }
}
