import {
  breakpoint
} from "./helpers";

export class LoginFormValidation {
  constructor(form) {
    this.$body = $('body');
    this.$form = form;
    this.$vkeyboard = $(document).find('.c-virtual-keyboard');
    this.$tncLink = this.$form.find('.c-login__tnc-link');
    this.$tncContent = this.$body.find('.c-login-overlay');
    this.$tncContentClose = this.$tncContent.find('.icon-close');
    // reset password strength
    this.resetPwdStrengthAttr();
  }

  init() {
    this.handleSubmit();
    this.addEventListeners();
  }

  handleSubmit() {
    // Intialize jquery validator custom methods
    this.intializeCustomMethods();
    // Intialize form validator
    this.validator = this.$form.validate({
      onfocusout: element => {
        if ($(element).val() == "") {
          $(element).closest(".c-login__input").addClass("error");
        } else {
          $(element).closest(".c-login__input").removeClass("error");
        }
      },
      rules: {
        report : {isSelected: true},
        inputLoginId : {
          required: true
        },
        inputLoginPassword : {
          required: true
        },
        inputPwd: {
          required: true,
          pwdCheck: true
        },
        inputRentePwd: {
          required: true,
          pwdEqualsTo: true
        },
        inputCardNumber: {
          required: true
        },
        inputPin: {
          required: true
        },
        terms: {
          required: true
        }
      },
      errorPlacement: (errorMap, errorList) => {
        // Conditions added to add custom required class for pwd fields
        // if(errorList[0].name =="inputPwd" || errorList[0].name =="inputRentePwd") {
        //   if(errorList.val() == ""){
        //     //errorList.next().text("The field is required");
        //     errorList.closest(".c-login__input").addClass("error");
        //   }
        // }else {
        //   if(errorList[0].name != "terms"){
        //     errorList.next().text("The field is required");
        //     errorList.closest(".c-login__input").addClass("error");
        //   }
        // }
      },
      submitHandler: form => {
        if (form.valid()) {
          form.submit();
        }
      }
    });
  }

  addEventListeners() {
    //this.$form.find("input.-pwd-valid").css({'background':"red"});
    this.$form.find("input").off().on('input', (e, info) => {
      let src = ""
      if (info) {
        src = info.src;
      }
      if (src !== 'virtual'){
        this.$vkeyboard.hide();
      }
      if (this.$form.valid()) {
        $('.-submit-btn').prop('disabled', false);
      } else {
        $('.-submit-btn').prop('disabled', 'disabled');
      }
    }).on('keypress', (e, info) => {
      if (e.target.maxLength > 0) {
        if ($(e.target).val().length >= parseInt(e.target.maxLength)) {
          if (info) {
            info.valid = false;
          }
          return false
        }
        else {
          if (info) {
            info.valid = true;
          }
        }
      }
    });
    this.$form.find(".c-input-select__wrap input").off().on('change', (e) => {
      if (this.$form.valid()) {
        $('.-submit-btn').prop('disabled', false);
      } else {
        $('.-submit-btn').prop('disabled', 'disabled');
      }
    });
    this.$form.find("input.-pwd-valid").on('focusin', (e) => {
      // show password requirements when user enters field.
      $(e.currentTarget).parent().find(".c-login__requirements").css("display", "block");
    }).on('blur', e => {
      // if input is not empty show password strength requirements
      if ($(e.currentTarget).val() === "") {
        $(e.currentTarget).parent().find(".c-login__requirements").css("display", "none");
        $(e.currentTarget).parent().find("li").removeClass("-add-tick");
      }
      else {
        $(e.currentTarget).parent().find(".c-login__requirements").css("display", "block");
      }
    }).on('input', e => {
      this.resetPwdStrengthAttr();
    });
    this.$tncLink.on('click', e => {
      e.preventDefault();
      this.showOverlayTnC();
      e.stopPropagation();
    });
    this.$tncContentClose.on('click', e => {
      this.hideOverlayTnC();
    });
    this.$body.on('keydown', e => {
      if (e.keyCode == 27) {
        this.hideOverlayTnC();
      }
    });
    $(document).on('click', e => {
      if ((!$(e.target).closest('.c-login-overlay__popup').length)) {
        this.hideOverlayTnC();
      }
    });
  }
  hideOverlayTnC() {
    this.$tncContent.removeClass('open');
    this.$body.removeClass('open-modal');
  }
  showOverlayTnC() {
    this.$tncContent.addClass('open');
    this.$body.addClass('open-modal');
  }
  // intialize jquery validator custom methods
  intializeCustomMethods() {
    // add custom methods for password comparison
    $.validator.addMethod("pwdEqualsTo", value => {
      if (value != $(".c-login__form").find(".-pwd-valid").val()) {
        let dataPasswordMismatchText = $(".c-login__form").find("input[name='inputRentePwd']").attr('dataPasswordMismatch');
        $(".c-login__form").find("input[name='inputRentePwd']").next().text(dataPasswordMismatchText);
        $(".c-login__form").find("input[name='inputRentePwd']").closest(".c-login__input").addClass("error");
        return false;
      }
      return true;
    });

    // add custom methods for password validation
    $.validator.addMethod("pwdCheck", value => {
      if ((value.length >= 6) || (/[a-z]/.test(value)) || (/\d/.test(value)) || (/[!@#\$%\^\&*\)\(+=._-]/g.test(value))) {
        if ((value.length >= 6)) {
          $(".c-login__form").find("li").eq(0).addClass("-add-tick");
          this.isValidLength = true;
        } else {
          $(".c-login__form").find("li").eq(0).removeClass("-add-tick");
          this.isValidLength = false;
        }
        if ((/[a-z]/.test(value))) {
          $(".c-login__form").find("li").eq(1).addClass("-add-tick");
          this.isLowerCase = true;
        } else {
          $(".c-login__form").find("li").eq(1).removeClass("-add-tick");
          this.isLowerCase = false;
        }
        if ((/\d/.test(value))) {
          $(".c-login__form").find("li").eq(3).addClass("-add-tick");
          this.isDigit = true;
        } else {
          $(".c-login__form").find("li").eq(3).removeClass("-add-tick");
          this.isDigit = false;
        }
        if ((/[!@#\$%\^\&*\)\(+=._-]/g.test(value))) {
          $(".c-login__form").find("li").eq(2).addClass("-add-tick");
          this.isSpecialChar = true;
        } else {
          $(".c-login__form").find("li").eq(2).removeClass("-add-tick");
          this.isSpecialChar = false;
        }
      } else {
        $(".c-login__form").find("li").removeClass("-add-tick");
        this.resetPwdStrengthAttr();
      }
      this.pwdStrengthCalc();
      return (value.length >= 6) && /[!@#\$%\^\&*\)\(+=._-]/g.test(value) && /[a-z]/.test(value) && /\d/.test(value);
    });
    // add custom method for validating selectBox
    $.validator.addMethod("isSelected", (value, element, arg) => {
      if ($(element).hasClass('-js-selected')) {
        $(element).addClass('valid');
        return true;
      }
      return false;
    });
  }

  // Intialize password strength meter
  pwdStrengthCalc() {
    if (this.isValidLength || this.isSpecialChar || this.isDigit || this.isLowerCase) {
      if (this.isValidLength && this.isSpecialChar && this.isDigit && this.isLowerCase) {
        this.resetPwdStrengthColor();
        this.$form.find(".c-login__input-pwd").eq(0).addClass("-pwd-green");
        this.$form.find(".c-login__input-pwd").eq(1).addClass("-pwd-green");
        this.$form.find(".c-login__input-pwd").eq(2).addClass("-pwd-green");
      } else {
        this.resetPwdStrengthColor();
        if (this.isSpecialChar && !this.isValidLength && !this.isDigit && !this.isLowerCase) {
          this.$form.find(".c-login__input-pwd").eq(0).addClass("-pwd-red");
        }
        if (this.isValidLength && this.isDigit && this.isLowerCase && !this.isSpecialChar ||
          this.isValidLength && this.isDigit && !this.isLowerCase && this.isSpecialChar ||
          this.isValidLength && !this.isDigit && this.isLowerCase && this.isSpecialChar ||
          !this.isValidLength && this.isDigit && this.isLowerCase && this.isSpecialChar) {
          this.$form.find(".c-login__input-pwd").eq(0).addClass("-pwd-yellow");
          this.$form.find(".c-login__input-pwd").eq(1).addClass("-pwd-yellow");
        }
        if (this.isValidLength && this.isSpecialChar && !this.isDigit && !this.isLowerCase ||
          this.isValidLength && this.isDigit && !this.isLowerCase && !this.isSpecialChar ||
          this.isValidLength && this.isLowerCase && !this.isDigit && !this.isSpecialChar ||
          this.isValidLength && this.isDigit && !this.isLowerCase && !this.isSpecialChar ||
          this.isSpecialChar && this.isDigit && !this.isLowerCase && !this.isValidLength ||
          this.isSpecialChar && this.isLowerCase && !this.isValidLength && !this.isDigit ||
          this.isDigit && this.isLowerCase && !this.isValidLength && !this.isSpecialChar) {
          this.$form.find(".c-login__input-pwd").eq(0).addClass("-pwd-red");
        }
      }
    } else {
      this.resetPwdStrengthAttr();
    }
  }

  // Reset password strength meter
  resetPwdStrengthAttr() {
    this.isValidLength = false;
    this.isSpecialChar = false;
    this.isLowerCase = false;
    this.isDigit = false;
    this.resetPwdStrengthColor();
  }

  // Reset password strength color
  resetPwdStrengthColor() {
    this.$form.find(".c-login__input-pwd").removeClass(function (index, className) {
      return (className.match(/(^|\s)-pwd-\S+/g) || []).join(' ');
    });
  }
}
