import { breakpoint } from "./helpers";
import { LoginFormValidation } from "../core/c-login-form-validation";

export class VirtualKeyboard {
  constructor($keyboard) {
    this.$keyboardObj = $keyboard;
    this.$key = $keyboard.find("input");
    this.$keyDelete = $keyboard.find(".-back-btn");
    this.$keyShift = $keyboard.find(".-caps-btn");
    this.$login = $(".c-login");
    this.$outputField = $(".c-login__input-text-box");
    this.$currentValue = this.$outputField.val();
    this.actionKeys = $keyboard.find(".-back-btn,.-caps-btn");
    this.$keyboardClose = $keyboard.find('.icon-close');

    const $form = this.$keyboardObj.parent().find('.c-login__form');
    this.form   = new LoginFormValidation($form);

  }

  init() {
    this.addEventListeners();
    this.form.init();
  }
  showVKeyboard(e, src) {
    let input = undefined;
    if (src === "window") {
      input = e[0];
    }
    else {
      input = e.target;
    }
    if (input.type) {
      if ((input.type != "text" && input.type != "password" && input.type != "number")) {
        this.$keyboardObj.css({'display': 'none'});
      }
      else {
        if(input.type == "number") {
          this.$key.not('.numeric').attr('disabled', 'disabled');
        }
        else {
          this.$key.removeAttr('disabled');
        }
        this.$outputField = $(input);
        //this.$keyboardObj.slideDown(700);
        let $parentInputDiv = this.$outputField.closest('.c-login__input');
        this.$keyboardObj.css({'display': 'block'});
        this.$keyboardObj.css({'top': $parentInputDiv.position().top + $parentInputDiv.outerHeight(true)});
        this.setCurrentVal();
      }
    }
  }
  hideVKeyboard() {
    this.$keyboardObj.css({'display': 'none'});
  }
  addEventListeners() {
    // slide keyboard down on focus
    this.$login.on("focus", ".c-login__input-text-box", e => {
      //show virtual keyboard on Medium, Large and X-Large viewports.
      if (!(['sm', 'xs']).includes(breakpoint())) {
        this.showVKeyboard(e);
      }
      else {
        this.hideVKeyboard();
      }
    });
    $(window).resize(e => {
      if (!(['sm', 'xs']).includes(breakpoint())) {
        //console.log(document.activeElement);
        this.showVKeyboard($(document.activeElement), "window");
      }
      else {
        this.hideVKeyboard();
      }
    });
    this.$keyboardClose.on("click", e => {
      this.hideVKeyboard();
    });
    // Slider should be open on click on keyboard
    this.$keyboardObj.on("click", e => {
      e.stopPropagation();
      e.preventDefault();
      return false;
    });

    // Slider up only when you click on document
    $(document).on("click", e => {
      if (e.target.type != "text" && e.target.type != "password" && e.target.type != "number") {
        this.$keyboardObj.css({'display': 'none'});
      }
    });

    // display typed key on input box and check if capslock is on
    this.$key.not(this.actionKeys).on("click", e => {
      e.stopPropagation();
      e.preventDefault();
      const $target = $(e.target);
      let keyCase, keyValue;
      // check for shift key for upper
      if (this.$keyShift.hasClass("-shift-activated")) {
        keyCase = "upper";
      } else {
        keyCase = "lower";
      }
      // handle case
      if (keyCase == "upper") {
        keyValue = $target.val().toUpperCase();
      } else {
        keyValue = $target.val().toLowerCase();
      }

      // grab current value
      let keypressFeedback = {src: "virtual", valid : true};
      let inputFeedback = {src: "virtual", valid : true};
      
      this.$outputField.trigger('keypress', [keypressFeedback]);
      this.$outputField.trigger('input', [inputFeedback]);
      //console.log(keypressFeedback);
      if (keypressFeedback.valid) {
        let output = this.$outputField.val();
        this.$outputField.val(output + keyValue);
        this.setCurrentVal(this.$outputField.val());
      }
      if (this.$outputField.hasClass("-pwd-valid")) {
        this.$outputField.trigger("focusin");
        this.$outputField.trigger("input");
      }
    });

    // delete key on clicking back button
    this.$keyDelete.on("click", e => {
      e.preventDefault();
      this.$outputField.val(
        this.$currentValue.substr(0, this.$currentValue.length - 1)
      );
      this.setCurrentVal();
    });

    // toggle capslock on clicking capslock button
    this.$keyShift.on("click", function(e) {
      e.preventDefault();
      $(this).toggleClass("-shift-activated");
    });
  }

  // grab current value of typed text
  setCurrentVal() {
    this.$currentValue = this.$outputField.val();
  }
}
