export class NpsPopover {
  constructor($modal) {
    this.$modal = $($modal);
  }
  init() {
    this.addEventListeners();
  }
  addEventListeners() {
    this.bankingBtns = $('.c-banking-nav__item');
    this.bankingBtns.each((i, btn) => {
      if (!$(btn).hasClass('is-active')) {
        $(btn)
          .on('click', e => {
            this.openModal();
          })
      }
    })

    this.$modal.find('.icon-close')
      .on('click', e => {
        this.closeModal();
      })
  }
  openModal() {
    this.$modal.addClass('open');
    $('body').addClass('open-modal')
  }
  closeModal() {
    this.$modal.removeClass('open');
    $('body').removeClass('open-modal')
  }
}
