/* 01 - imports */
import {
  breakpoint
} from './helpers';

export class CompCollapse {
  constructor($collapse) {
    this.$collapse = $collapse;
    this.$collapseBtn = this.$collapse.find('.o-comp-collapse__btn');
  }
  init() {
    this.addEventListeners();
  }
  addEventListeners() {
    $(window).on("load resize", e => {
      if (breakpoint() === 'xs' || breakpoint() === 'sm' || breakpoint() === 'md'){
        if (this.$collapse.parent().parent()[0].className.includes('footer') && breakpoint() === 'md'){
          this.$collapse.removeClass('collapsed');
        }else{
          this.$collapse.addClass('collapsed');
        }
        this.$collapseBtn.off().on('click', e => {
          e.stopPropagation();
          e.preventDefault();
          if (this.$collapse.parent().parent()[0].className.includes('footer') && breakpoint() === 'md'){
            this.$collapse.removeClass('collapsed');
          }
          if (this.$collapse.hasClass('collapsed')) {
            this.resetOpen();
            this.$collapse.removeClass('collapsed');
          } else {
            this.$collapse.addClass('collapsed');
          }
        })
      } else {
        if (this.$collapse.hasClass('collapsed')){
          this.$collapse.removeClass('collapsed');
        }
        this.$collapseBtn.off('click');
      }  
    });
  }

  resetOpen() {
    $('.o-comp-collapse').each((i,elem) => {
      if(!$(elem).hasClass('collapsed')) {
        if ($(elem).parent().parent()[0].className.includes('footer') && breakpoint() === 'md'){
          $(elem).removeClass('collapsed');
        }else{
          $(elem).addClass('collapsed');
        }
      }
    })
  }
}
