  /* 02 - export CompareProductsSection class */
  export class CompareProductsSection {
    constructor($compareproducts) {
      this.$compareproducts = $compareproducts;
      this.$viewMoreButton = this.$compareproducts.find('.view-more-btn');
      this.$viewLessButton = this.$compareproducts.find('.view-less-btn');
      this.$viewMoreContent = this.$compareproducts.find('.c-compare-carousel__more-content');
    }

    init() {
      this.addEventListeners();
    }

    addEventListeners() {

        this.$viewMoreButton
        .on('click', e => {
          e.preventDefault();
          this.$viewMoreButton.addClass('hidden');
          this.$viewLessButton.removeClass('hidden');
          this.$viewMoreContent.removeClass("-close");
        })

        this.$viewLessButton
        .on('click', e => {
          e.preventDefault();
          this.$viewLessButton.addClass('hidden');
          this.$viewMoreButton.removeClass('hidden');
          this.$viewMoreContent.addClass("-close");
        })
    }
  }
