import { Modal } from "../core/c-modal";

export class CmsContent {
  constructor($content) {
    this.$content = $content;
    this.$modalImage = this.$content.find('.image-popup');
    const $modal = this.$content.find('.c-modal');
    this.modal = new Modal($modal);
    this.modal.init();
  }
  init() {
    this.generateHeadings();
    this.addEventListeners();
  }

  addEventListeners() {
    this.$content.find('.expand-link')
      .on('click', e => {
        e.preventDefault();
        e.stopPropagation();
        const $target = $(e.target);

        if ($target.hasClass('is-active')) {
          $target.next().slideDown('fast');
          $target.removeClass('is-active');
        }
        else {
          $target.next().slideUp('fast');
          $target.addClass('is-active');
        }
      })

      this.$modalImage
      .off()
      .on('click', e => {
        e.stopPropagation();
        e.preventDefault();
        this.modal.openModal();
      })

  } 

  generateHeadings() {
    this.$content.find('table').each ((i,elem) => {
      if ($(elem).hasClass('c-cms-content-table-responsive')) {
        $(elem).find('td')
        .each((i, elem) => {
          let headingText;
          const 
            tdIndex = $(elem).index(),
            $tr = $(elem).closest('table').find('tr');
          if(!$tr.find('th').eq(tdIndex).attr('data-label')) {
            headingText = $tr.find('th').eq(tdIndex).text();
            $(elem).attr('data-label', headingText);
          }
        });
      }
    });
  }
}