import {
  isRtl
} from "./helpers";

export class Carousel {
  constructor($carousel, type) {
    this.$carousel = $carousel;
    this.isRtl = isRtl();
    this.xsDisplay = this.xsCarousel(type);
    this.mobileDisplay = this.mobileCarousel(type);
    this.tabletDisplay = this.tabletCarousel(type);
    this.desktopDisplay = this.desktopCarousel(type);
  }

  init(carouselName) {
    this.initCarousel(carouselName);
  }

  initCarousel(carouselName) {
    let hasDots = carouselName === 'photo-gallery';
    if (carouselName == 'photo-gallery') {
      if (this.$carousel.find('.c-photo-gallery__photo').length > 1 &&
        this.$carousel.parents('.-article-content').length > 0) {
        this.$carousel.owlCarousel({
          rtl: this.isRtl,
          nav: true,
          dots: hasDots,
          responsiveClass: true,
          navText: [
            "<div class='o-carousel-button-left icon-next'></div>",
            "<div class='o-carousel-button icon-next'></div>"
          ],
          loop: true,
          responsive: {
            0: this.xsDisplay,
            480: this.mobileDisplay,
            768: this.tabletDisplay,
            992: this.desktopDisplay
          },
        })
      }
      else {
        this.$carousel.addClass("-js-single-item").removeClass('owl-carousel');
      }
    } 
    else {
      this.$carousel.owlCarousel({
        rtl: this.isRtl,
        nav: true,
        dots: hasDots,
        responsiveClass: true,
        navText: [
          "<div class='o-carousel-button-left icon-next'></div>",
          "<div class='o-carousel-button icon-next'></div>"
        ],
        loop: true,
        responsive: {
          0: this.xsDisplay,
          480: this.mobileDisplay,
          768: this.tabletDisplay,
          992: this.desktopDisplay
        },
      })
    }
  }
  xsCarousel(type) {
    switch (type) {
      case 'news':
        return {
          margin: 30,
            items: 1.3,
            nav: false,
            center: false
        }
        break;
      case 'promotions':
        return {
          items: 1.3,
            nav: false,
            margin: 20,
            center: true
        }
        break;
      case 'videos-related':
        return {
          items: 1.3,
            nav: false,
            margin: 20,
            center: true
        }
        break;
      case 'events':
        return {
          margin: 30,
            items: 1.3,
            nav: false,
            center: false
        }
        break;
      case 'photo-gallery':
        return {
          margin: 30,
            items: 1,
            nav: false,
            center: true
        }
        break;
      case 'compare-carousel':
        return {
          dots: true,
            margin: 30,
            items: 1.3,
            nav: false,
            center: false
        }
        break;
      case 'about-highlight':
        return {
          dots: false,
            margin: 30,
            items: 1.3,
            nav: false,
            center: false
        }
        break;
    }
  }

  mobileCarousel(type) {
    switch (type) {
      case 'news':
        return {
          margin: 30,
            items: 1.3,
            nav: false,
            center: false
        }
        break;
      case 'promotions':
        return {
          margin: 30,
            items: 2,
            nav: false,
            center: true
        }
        break;
      case 'videos-related':
        return {
          margin: 30,
            items: 2,
            nav: false,
            center: true
        }
        break;
      case 'events':
        return {
          margin: 30,
            items: 1.3,
            nav: false,
            center: false
        }
        break;
      case 'photo-gallery':
        return {
          margin: 30,
            items: 1,
            nav: false,
            center: true
        }
        break;

      case 'compare-carousel':
        return {
          dots: true,
            margin: 20,
            items: 2,
            nav: false,
            center: false
        }
        break;
      case 'about-highlight':
        return {
          dots: false,
            margin: 30,
            items: 1.3,
            nav: false,
            center: false
        }
        break;
    }
  }

  tabletCarousel(type) {
    switch (type) {
      case 'news':
        return {
          margin: 50,
            items: 2,
            nav: false,
            center: false,
        }
        break;
      case 'promotions':
        return {
          margin: 30,
            items: 2,
            nav: false,
            center: true
        }
        break;
      case 'videos-related':
        return {
          margin: 30,
            items: 2,
            nav: false,
            center: true
        }
        break;
      case 'events':
        return {
          margin: 50,
            items: 2,
            nav: false,
            center: false,
        }
        break;
      case 'photo-gallery':
        return {
          margin: 0,
            items: 1,
            nav: false,
            center: true
        }
        break;
      case 'compare-carousel':
        return {
          dots: true,
            margin: 40,
            items: 2.9,
            nav: false,
            center: false
        }
        break;
      case 'about-highlight':
        return {
          dots: false,
            margin: 30,
            items: 4,
            nav: false,
            center: false,
            loop: false
        }
        break;
    }
  }

  desktopCarousel(type) {
    switch (type) {
      case 'news':
        return {
          dots: true,
            margin: 30,
            items: 2.99
        }
        break;
      case 'promotions':
        return {
          dots: true,
            margin: 30,
            items: 2
        }
        break;
      case 'videos-related':
        return {
          dots: true,
            margin: 50,
            items: 2.99
        }
        break;
      case 'events':
        return {
          dots: true,
            margin: 30,
            items: 2.99
        }
        break;
      case 'photo-gallery':
        return {
          margin: 0,
            items: 1,
            center: true
        }
        break;
      case 'compare-carousel':
        return {
          dots: true,
            margin: 20,
            items: 4
        }
        break;
      case 'about-highlight':
        return {
          dots: true,
            margin: 30,
            items: 4,
            loop: false,
            nav: false
        }
        break;
    }
  }
}
