import * as d3 from "d3";
export class DoughnutChart {
  constructor($selector) {
    this.id = '#' + $selector[0].id;
    this.divNode = d3.select("body").node();
    this.color = ['#ec0100', '#ba1b19', '#751515', '#510c0b']
  }

  init() {
    this.createChart();
  }

  getTotal(data) {
    const result = data.reduce((acc, i) => {
      return acc + i.num
    }, 0);
    return result;
  }

  getPercentage(partial, total) {
    return (partial / total * 100).toFixed(2);
  }

  sortData() {
    let _data = $(".c-chart").data("chart");
    const total = this.getTotal(_data);

    _data.sort(function (a, b) {
      if (a.num > b.num) {
        return -1
      }
      if (a.num < b.num) {
        return 1;
      }
      return 0;
    });
    _data.forEach((d, i) => {
      d.color = this.color[i]
    })

    _data.forEach((d, i) => {
      d.percentage = this.getPercentage(d.num, total)
    })
    return _data;
  }

  // we create the pie chart with the sorted order we want
  createPie() {
    //in this case we need to invert the order 
    let pie = d3.pie().sort(function (a, b) {
        if (a.num > b.num) {
          return 1
        }
        if (a.num < b.num) {
          return -1;
        }
        return 0;
      })
      .value(function (d) {
        return d.num;
      });

    return pie;
  }

  //full range of colors
  coloRange() {
    return d3.scaleOrdinal().range(this.color);
  }

  createLegend(data) {
    //create the legend divs deppending on the values
    var legend = d3.select(this.id)
      .append("div")
      .attr("class", "c-chart__legend")
      .selectAll("legend")
      .data(data).enter()
      .append("div")
      .attr("class", "c-legend__row");

    //append circles
    var svgContainer = legend.append("svg")
      .attr("width", 15)
      .attr("height", 15);

    var circle = svgContainer.append("circle")
      .attr("class", "c-legend__circle")
      .attr("cx", 5)
      .attr("cy", 8)
      .attr("r", 5)
      .style('fill', (d) => d.color);

    //Append Legend Text      
    legend.append("text")
      .attr("class", "c-legend__text")
      .text((d) => d.str_lab);

  }
  getCircle(data) {
    var svgContainer = d3.select(".c-tooltip__circle")
      .attr("cx", 5)
      .attr("cy", 10)
      .attr("r", 5)
      .style('fill', data.color);
  }

  arcHover(radius) {
    return d3.arc()
      .outerRadius(radius + 10)
      .innerRadius(radius - 80);
  }

  addMouseEvents(g, arc, radius) {
    let color = this.coloRange();
    g.append("path")
      .attr("d", arc)
      .style("fill", function (d) {
        return color(d.data.num);
      })
      .on("mouseover", (d, i, nodes) => {
        d3.select(nodes[i])
          .attr("d", this.arcHover(radius));
      })
      .on("mousemove", (d, i, nodes) => {
        d3.select(nodes[i])
          .transition()
          .duration(500)
          .ease(d3.easeElastic);
        var mousePos = d3.mouse(this.divNode);
        d3.select("#main_tooltip")
          .style("left", mousePos[0] - 40 + "px")
          .style("top", mousePos[1] - 70 + "px")
          .select("#value")
          .attr("text-anchor", "middle");
        d3.select(".c-tooltip__title").text(d.data.str_lab);
        //append circles
        this.getCircle(d.data);

        d3.select(".c-tooltip__text").text(`${d.data.percentage}%`);
        d3.select("#main_tooltip").classed("hidden", false);
      })
      .on("mouseout", function (d) {
        d3.select(this)
          .style("filter", "none");
        d3.select(this)
          .transition()
          .duration(500)
          .attr('transform', 'translate(0,0)')
          .attr("d", arc);
        d3.select("#main_tooltip").classed("hidden", true);
      });
  }

  createChart() {
    var data = this.sortData();
    var width = 400,
      height = 400,
      radius = (Math.min(width, height) / 2) - 20;

    var arc = d3.arc()
      .outerRadius(radius - 10)
      .innerRadius(radius - 80);
    var pie = this.createPie();
    d3.select(this.id).append("div")
      .attr("id", "mainPie")
      .attr("class", "c-chart__pie-box");

    var svg = d3.select("#mainPie").append("svg")
      .attr("id", "donut-chart")
      .attr("width", width)
      .attr("height", height)
      .append("g")
      .attr("transform", "translate(" + width / 2 + "," + height / 2 + ")");

    var g = svg.selectAll(".arc")
      .data(pie(data))
      .enter().append("g")
      .attr("class", "arc")
      .attr("stroke", "#fff")
      .attr("stroke-width", "4px");

    this.createLegend(data);

    this.addMouseEvents(g, arc, radius);
  }
}
