import {
  Modal
} from "../core/c-modal";

export class EventGallery {
  constructor($selector) {
    this.$selector = $selector;
    this.$card = $selector.parent().parent();
    this.$content = this.$card.find('.c-event-gallery');
    
    const $modal = $('.js-events-carousel').parent().find('.c-modal');
    this.modal = new Modal($modal);
  }

  init() {
    this.addEventListeners();
    this.modal.init();
  }

  addEventListeners() {
    //console.log(this.$card);
    this.$card
      .off()
      .on('click', e => {
        e.stopPropagation();
        e.preventDefault()
      });

    this.$selector
      .on('click', e => {
        e.stopPropagation();
        e.preventDefault();
        this.modal.openModal(this.$content);
      })
  }
}
