export class CardsForm {
    constructor($form) {
        this.$form = $form;
        this.$options = this.$form.find('.c-input-select__option');
    }
    init(){
        this.addEventListeners();
    }
    addEventListeners() {
        this.$options.each((i,option) => {
            $(option)
                .on('click', e => {
                    this.$form.find('form').submit();
                })
        })
    }
}