export class ProgressBar {
    constructor($selector) {
        this.$progressBar = $selector;
        this.$bar = this.$progressBar.find('.bar');
        this.duration = parseInt(this.$progressBar.attr('data-duration')) * 1000;
        //console.log(this.duration);
    }
    init() {
        this.moveBar();
    }

    moveBar() {
        let width = 1;
        const interval = setInterval(frame, this.duration / 100);
        function frame(){
            if (width >= 100) {
                clearInterval(interval);
            } else {
                width++;
                $('.bar').css({ width: width + '%' });
            }
        }
    }
}
