/* ==============================================================================
Index
----------------------------------------
01 - imports
02 - export MainHeader class
03 - cache DOM
04 - cache "data-" attributes to allow customization
05 - transition if "data-anim" true
06 -
07 -
08 -
09 -
============================================================================== */
/* 01 - imports */
import {
  dimmerToggle,
  windowportSize
} from './helpers.js';

/* 02 - export MainHeader class */
export class MainHeader {
  constructor($header) {
    this.$header = $header;
    this.isPremium = this.$header.attr('data-theme') === 'premium';
    this.isPersonal = this.$header.find('.o-site-logo').length ? true : false;
    this.isPageScrolled = false;
    this.isNavExpanded = false;
    this.$menuButton = this.$header.find('.o-menu-button');
    this.$closeButton = this.$header.find('.o-close-button');
    this.$mobileBankingButton = this.$header.find('.js-banking-nav-toggle');
    this.$desktopBankingButton = this.$header.find('.js-banking-nav-toggle-desktop');
    this.$mobileBankingNav = this.$header.find('.c-banking-nav__navigation');
    this.$searchButton = this.$header.find('.js-toggleSearchBox');
    this.$loginButton = this.$header.find('.c-quick-login');
    this.$loginDropdown = this.$header.find('.c-quick-login__list');
    this.$searchBox = this.$header.find('.c-search');
    this.$searchCloseBtn = this.$searchBox.find('.js-toggleSearchBox');
    this.$searchInput = this.$searchBox.find('input');
    this.$mainNav = this.$header.find('.c-main-nav');
    this.$bankingNav = this.$header.find('.c-banking-nav');
    this.$logoText = this.isPersonal ? this.$header.find('.o-site-logo__text') : this.$header.find('.o-site-logo-general__text');
    this.$logoIcon = this.isPersonal ? this.$header.find('.o-site-logo__icon') : this.$header.find('.o-site-logo-general__icon');
    this.$loginInnerButton = this.$header.find('.btn-login');
    this.$mainMenuList = this.$mainNav.find('.nav-lvl li');
    this.$mdPlusMenuCategories = this.$mainNav.find('-js-md-plus-visible-nav-lvl');
    this.$smMinusMenuCategories = this.$mainNav.find('-js-sm-minus-visible-nav-levels');
    this.$mapButton = this.$header.find('.js-show-map');

    this.$rightMenuList = this.$mainNav.find('.nav-lvl01__item');
    this.$switchToLink = this.$mainNav.find('.o-switch-button__text');
    this.$backToLink = this.$mainNav.find('.o-switch-button__backtext');
    this.$topMenu = this.$header.find('.top-menu');
    this.prevScrolltop = 0;
    this.lockTouchScreen = false;
  }

  init() {
    this.addEventListeners();
    this.evalWindowOffset();
    this.checkScrollMenu();
    this.mobileTopmenu();
    if (this.isPersonal) {
      this.initPersonalLogo();
    }
  }

  evalWindowOffset() {
    this.setScrollFlag();
    this.toggleStickyHeader(this.isPageScrolled);
  }

  addEventListeners() {

    this.$menuButton
      .on('click', e => {
        e.preventDefault();
        this.toggleLogin(true);
        this.toggleSearch(true);
        this.toggleMainNav();
      })

    this.$closeButton
      .on('click', e => {
        e.preventDefault();
        this.toggleLogin(true);
        this.toggleSearch(true);
        this.toggleMainNav();
      })

    this.$mobileBankingButton
      .on('click', e => {
        e.preventDefault();
        this.toggleLogin(true)
        this.toggleSearch(true);
        this.toggleMainNav(true);
      })

    this.$mobileBankingNav
      .on('click', e => {
        e.preventDefault();
        this.toggleLogin(true)
        this.toggleSearch(true);
        this.toggleMainNav(true);
      })

    this.$searchButton
      .on('click', e => {
        e.preventDefault();
        this.toggleLogin(true)
        this.toggleMainNav(true);
        this.toggleSearch();
      })

    this.$header
      .on('click', e => {
        e.stopPropagation();
      })

    this.$searchCloseBtn
      .on('click', e => {
        e.preventDefault();
        this.toggleSearch(true);
      })

    this.navLinkEvents();

    this.$rightMenuList.each((i, level1menuitem) => {
      let portSize = windowportSize();
      if (portSize === 'md' || portSize === 'lg' || portSize === 'xl') {
        return;
      }
      if ($(level1menuitem).children('ul').length == 0) {
        return;
      } else {
        $(level1menuitem).on('click', e => {
          let menuLevel = $('.nav-lvl').attr('menu-level');
          //First Menu Level on xs, sm has been clicked before
          if (menuLevel > 1) {
            return;
          }
          e.preventDefault();
          $('.nav-lvl__item').addClass('is-hidden');
          $('.nav-lvl01__item').addClass('is-hidden');
          $(level1menuitem).removeClass('is-hidden collapsed');
          $(e.currentTarget).find('.nav-lvl01__link').addClass('nav-lvl__item is-active').removeClass('nav-lvl01__link');
          $('.nav-levels').addClass('nav-levels__secondlevel');
          /*Remove switch to text link and add instead of it back link*/
          this.$switchToLink.addClass('hidden');
          this.$backToLink.removeClass('hidden');
          this.$searchButton.addClass('hidden');
          this.$mapButton.addClass('hidden');

          let mainsectiontext = $('.nav-lvl').find('.is-active').first().text();
          $('#backto-main-section').text(mainsectiontext);
          $('.nav-lvl').attr('menu-level', "2");
        });
      }
    })
    this.clickBackLink();

    $(document).on('click touchstart', e => {
      this.toggleSearch(true);
      this.toggleMainNav(true);

      var portSize = windowportSize();

      if (portSize === 'xs' || portSize === 'sm') {
        if(!this.lockTouchScreen) {
          this.$loginDropdown.removeClass('is-expanded');
          this.$loginInnerButton.removeClass('is-active');
        }
      }else {
        this.$loginDropdown.removeClass('is-expanded');
        this.$loginInnerButton.removeClass('is-active');
      }
      this.lockTouchScreen = false;

      $('.c-main-nav .nav-lvl__item').removeClass('is-active');
      $('.c-main-nav .nav-lvl01').addClass('hide');
    })

    $(window).resize(e => {
      this.toggleCollapse();
      $('.nav-lvl__item a').addClass('is-hidden');
      this.navLinkEvents();
      this.clickBackLink();
      this.mobileTopmenu();
    });

    this.$mainNav
      .on('click touchstart', e => {
        e.stopPropagation();
      })

    this.$loginDropdown
     .on('click touchstart', e => {
       e.stopPropagation();
     })

     this.$loginDropdown.find('.c-quick-login__item').each((i, loginItem)  => {
        $(loginItem).on('click', e => {
         this.toggleLogin(true);
       });
     })

    this.$loginButton
      .on('click', e => {
          this.lockTouchScreen = false;
          this.toggleMainNav(true);
          this.toggleSearch(true);
          this.toggleLogin(false);
      })

    this.$header
      .on('mouseenter', e => {
        this.toggleWhiteBackground(true);
        if (!this.isPersonal) {
          this.animateLogo();
        } else {
          this.animatePersonalLogo();
        }
      })
      .on('mouseleave', e => {
        if (!this.isPersonal) {
          this.animateLogoOut();
        }
        if (!this.isNavExpanded && !this.isPageScrolled) {
          this.toggleWhiteBackground(false);
        }
      })

    $(window)
      .on('scroll', e => {
        this.setScrollFlag();
        this.toggleStickyHeader(this.isPageScrolled);
        let isWhite = this.isPageScrolled;
        if (this.isNavExpanded) isWhite = true;
        this.toggleWhiteBackground(isWhite);
      })
  }

  initPersonalLogo() {
    this.$logoText.on('webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend', () => {
      this.$logoText.removeClass('animate')
      this.$logoText.css({
        'width': '0'
      })
    })
  }

  animatePersonalLogo() {
    if (!this.$logoText.hasClass('animate')) {
      if (!this.$logoText.hasClass('hover-animate')) {
        this.$logoText.addClass('hover-animate');
        this.$logoText.on('webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend', () => {
          this.$logoText.removeClass('hover-animate')
        })
      }
    }
  }

  animateLogoOut() {
    this.$logoText.removeClass('-animate');
    this.$logoIcon.removeClass('-animate');
    this.$logoIcon.on('webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend', () => {
      this.$logoIcon.removeClass('-animating');
      this.$logoIcon.removeClass('-animate');
      this.$logoText.removeClass('-animate');
      this.$logoIcon.off();
    });
  }

  animateLogo() {
    if (!this.$logoText.hasClass('-animate') && !this.$logoIcon.hasClass('-animating')) {
      this.$logoIcon.off();
      this.$logoText.addClass('-animate');
      this.$logoIcon.addClass('-animating');
      this.$logoIcon.addClass('-animate');
    }
  }

  checkScrollMenu() {

    let portSize = windowportSize();
    let selectedNavHeight = $('.nav-levels').find('.show').height();
    let windowHeight = $(window).height() - $('.nav-footer').height() - 148;

    let hasShadow = $('.nav-footer').hasClass('show-shadow');

    if (hasShadow) {
      $('.nav-footer').removeClass('show-shadow');
    }

    if (selectedNavHeight > windowHeight || portSize === 'xs' || portSize === 'sm') {
      $('.nav-footer').addClass('show-shadow');
    }
  }

  mobileTopmenu() {
    var portSize = windowportSize();
    if (portSize === 'xs' || portSize === 'sm') {
      if (this.$topMenu.hasClass('hidden')) {
        this.$topMenu.removeClass('hidden');
      }
      $('.site-header__content').addClass('top-main-header');
      $('.c-breadcrumbs').addClass('-top-padding');

      this.$header
        .off('mouseenter')
        .off('mouseleave');
    } else {
      $('.site-header__content').removeClass('top-main-header');
      $('.c-breadcrumbs').removeClass('-top-padding');
    }
  }

  toggleLogin(reset = false) {

    const isExpanded = !reset ? this.$loginDropdown.hasClass('is-expanded') : true;

    if (isExpanded) {
      this.$loginDropdown.removeClass('is-expanded');
      this.$loginInnerButton.removeClass('is-active');
    } else {
      this.$loginDropdown.addClass('is-expanded');
      this.$loginInnerButton.addClass('is-active');
    }
    this.lockTouchScreen = true;
  }

  toggleSearch(reset = false) {
    const isExpanded = !reset ? this.$searchBox.hasClass('is-visible') : true;
    if (isExpanded) {
      this.$searchBox.removeClass('is-visible');
      this.isNavExpanded = false;
      // Fixing the focus issue in android mobile // start
      // To close keypad in all mobile when search close
      // TODO: To vertify the double trigger below
      this.$searchInput.blur();
      // Fixing the focus issue in android mobile // stop
      dimmerToggle(false, false, 'fast');
    } else {
      this.$searchBox.addClass('is-visible');
      this.isNavExpanded = true;
      // Fixing the focus issue in android mobile // start
      // TODO: To vertify the double trigger below
      this.$searchInput.trigger('focus');
      // Fixing the focus issue in android mobile // stop
      dimmerToggle(true, false, 'fast');
    }
  }

  setScrollFlag() {
    this.prevScrolltop = $(window).scrollTop();
    if ($(window).scrollTop() > 0) {
      this.isPageScrolled = true;
    } else {
      this.isPageScrolled = false;
    }
  }

  toggleStickyHeader(isSticky) {
    if (isSticky) {
      this.$header.addClass('is-sticky');
      $('.site-header__content').removeClass('top-main-header');
      this.$topMenu.addClass('hidden');
    } else {
      let portSize = windowportSize();
      this.$header.removeClass('is-sticky');
      if (portSize === 'xs' || portSize === 'sm') {
        $('.site-header__content').addClass('top-main-header');
      }
      this.$topMenu.removeClass('hidden');
    }
  }

  toggleWhiteBackground(isWhite) {
    if (isWhite) {
      this.$header.removeClass('t-premium__header');
    } else if (this.isPremium) {
      this.$header.addClass('t-premium__header');
    }
  }

  toggleMainNav(reset = false) {
    const isExpanded = !reset ? this.$mainNav.hasClass('is-expanded') : true;
    let portSize = windowportSize();

    if (isExpanded) {
      this.isNavExpanded = false;
      this.$menuButton.removeClass('is-active');
      this.$mainNav.removeClass('is-expanded');
      this.$header.removeClass('is-header-transparent');
      this.$closeButton.addClass('hidden');
      this.$menuButton.blur();
      this.$switchToLink.addClass('hidden');

      this.$backToLink.trigger('click');

     if (portSize === 'xs' || portSize === 'sm') {
        $('.c-main-nav  .nav-lvl01__item').each((i, navItem) => {
            $(navItem).removeClass('-animate-back');
        })
      }

      $('body').removeClass('body-menu-opened');

      dimmerToggle(false, false, 'fast');
      $('.menu-footer-bar').addClass('hide');
      $('.menu-bar').removeClass('hidden');

    } else {
      this.isNavExpanded = true;
      this.$menuButton.addClass('is-active');
      this.$mainNav.addClass('is-expanded');
      this.$header.addClass('is-header-transparent');
      this.$closeButton.removeClass('hidden');
      this.$switchToLink.removeClass('hidden');

      $('body').addClass('body-menu-opened');

      this.showFirstMenu();
      this.checkScrollMenu();
      dimmerToggle(true, false, 'fast');
      $('.menu-footer-bar').removeClass('hide');
      $('.menu-bar').addClass('hidden');
    }

    this.toggleCollapse();
  }

  toggleCollapse() {
    let portSize = windowportSize();
    if (portSize === 'xs' || portSize === 'sm') {
      this.$rightMenuList.addClass('collapsed');
    } else {
      this.$rightMenuList.removeClass('collapsed');
    }
  }

  showFirstMenu() {

    if ($('.nav-lvl__item').hasClass('is-active')) {
      $('.nav-lvl__item.is-active').prev().children('li.nav-lvl__item').addClass('is-active');
    } else {
      $('.nav-lvl01').eq(0).removeClass('hide').addClass('show').prev().children('li.nav-lvl__item').addClass('is-active');
    }
  }

  toggleMainItemColors(item) {
    this.$mainMenuList.removeClass('is-active');
    $(item).addClass('is-active');
  }

  toggleSubMenu(subMenu) {
    $('.c-main-nav  .nav-lvl01').slideUp().removeClass('show')
    const clickedMenu = $(subMenu).parent().next();
    if (clickedMenu.hasClass('hide')) {
      clickedMenu.removeClass('hide');
    } else {
      clickedMenu.slideDown();
    }
  }

  navLinkEvents() {
    this.$mainMenuList.each((i, mainMenuItem) => {
      let mainMenuCount = $('.nav-levels').find('ul.nav-lvl').length;
      let portSize = windowportSize();
      if (portSize === 'xs' || portSize === 'sm') {
        $(mainMenuItem).off('mouseenter').off('click');
        $(mainMenuItem).on('click', (event) => {

          if(!$(mainMenuItem).hasClass('is-active')) {
            /*If the Menu is not currently opened and active*/
            this.toggleMainItemColors(mainMenuItem);
            this.toggleSubMenu(event.currentTarget);
            this.checkScrollMenu();
          }
        })
      } else {
        $(mainMenuItem).off('click').off('mouseenter');
        $(mainMenuItem).on('mouseenter', (event) => {
          this.$mainMenuList.removeClass('is-active');
          $(mainMenuItem).addClass('is-active');
          this.$mainMenuList.eq(i + mainMenuCount).addClass('is-active').parent().next().addClass('show');

          $('.c-main-nav  .nav-lvl01').removeClass('show').addClass('hide');
          $('.c-main-nav  .nav-lvl01').eq(i).removeClass('hide').addClass('show');

          this.checkScrollMenu();
        })
      }
    })
  }

  clickBackLink() {
    this.$backToLink
      .on('click', e => {
        let portSize = windowportSize();
        if (portSize === 'xs' || portSize === 'sm') {
          this.$backToLink.css({
            'visibility': 'hidden'
          });

          $('.c-main-nav  .nav-lvl01__item').each((i, navItem) => {
            if (!$(navItem).hasClass('is-hidden')) {
              $(navItem).addClass('-animate-back');
            }
          })
          /*Remove switch to text link and add instead of it back link*/

          setTimeout(() => {
            this.$backToLink.css({
              'visibility': 'visible'
            });

            this.$backToLink.addClass('hidden');
            this.$switchToLink.removeClass('hidden');
            this.$closeButton.removeClass('hidden');
            $('.c-main-nav  .nav-lvl__item').removeClass('is-hidden');
            $('.c-main-nav  .nav-lvl01__item').removeClass('-animate-back');
            $('.c-main-nav  .nav-lvl01__item').removeClass('is-hidden').addClass('collapsed');
            $('.c-main-nav .nav-lvl01__item').find('.nav-lvl__item.is-active').removeClass('nav-lvl__item is-active').addClass('nav-lvl01__link');
            $('.c-main-nav  .nav-levels').removeClass('nav-levels__secondlevel');
            this.$searchButton.removeClass('hidden');
            this.$mapButton.removeClass('hidden');
          }, 400)
        } else {
          $('.c-main-nav .nav-lvl__item').removeClass('is-hidden');
        }
        $('.c-main-nav  .nav-lvl').attr('menu-level', "1");
      })
  }
}
