import { evalCaptcha } from "./helpers";

export class WriteUsForm {
  constructor($formWrapper) {
    this.$formWrapper = $formWrapper;
    this.$form = this.$formWrapper.find('form');
    this.formType = 'complaint';
    this.$bankTold = this.$formWrapper.find('.c-contact-form__bank-told');
    this.$radioFormType = this.$formWrapper.find('.js-form-type').find('input');
    this.$radioToldBank = this.$formWrapper.find('.js-bank-told').find('input');
    this.$buttonClear = this.$formWrapper.find('.js-clear');

    ADCB.initRecaptchaCallback();

  }
  init() {
    this.addEventListeners();
    this.possibleInputs = this.getPossibleFormFields();
    this.evalForm();
    this.handleSubmit();
  }

  handleSubmit() {
    this.validator = this.$form.validate({
      errorPlacement: (errorMap, errorList) => {
        // Do nothing here
      },
      submitHandler: form => {
        if (evalCaptcha(this.$form)) {
          form.submit();
        }
      }
    });

  }

  addEventListeners() {
    this.$radioToldBank.each((i, button) => {
      $(button)
        .on('click', e => {
          const displayAdditional = e.target.value === 'yes';
          this.toggleAdditional(displayAdditional);
        });
    });
    this.$radioFormType.each((i, button) => {
      $(button)
        .on('click', e => {
          const formType = e.target.value;
          this.setFormType(formType);
          this.validator.resetForm();

        })
    })
   this.$buttonClear
      .on('click', e => {
        this.validator.resetForm();
      })
  }

  getTitle(category) {
    const titleFilterObj = {
      suggestion: 'Suggestion',
      contact: 'Contact us',
      commendation: 'Commendation/ Appreciation',
      complaint: 'Complaint',
      shareholder: 'Shareholder Feedback'
    }

    return titleFilterObj[category]
  }

  setFormType(formType) {
    const $formTitle = this.$formWrapper.find('.c-contact-form__subtitle--type');
    $formTitle.html(this.getTitle(formType));
    this.$form.attr('name', formType);
    this.formType = formType;
    this.evalForm();
  }

  evalForm() {
    const
      $formFields = this.$form.find('.o-form__field , .o-form__field--text-area , .o-form__field--additional');

    $formFields.each((i, $field) => {
      $field = $($field);
      this.evalFormField($field);
    })
  }

  evalFormField($field) {
    const
      $fieldInput = $field.find('input'),
      $fieldTextArea = $field.find('textarea');

    this.filterInputs($fieldInput);
    this.filterInputs($fieldTextArea);
  }

  filterInputs($inputs) {
    const formTypeInputs = this.possibleInputs[this.formType];

    if ($inputs.length > 0) {
      $inputs.each((i, $input) => {
        $input = $($input);
        const
          inputName = $input.attr('name'),
          isCorrectInput = formTypeInputs.indexOf(inputName) >= 0;

        let $inputContainer = $input.parent().parent();

        if ($inputContainer.hasClass('c-input-select')) {
          $inputContainer = $inputContainer.parent().parent();
        }

        if (isCorrectInput) {
          $inputContainer.css('display', 'flex');
        } else {
          $inputContainer.css('display', 'none');
        }
      })
    }
  }

  toggleAdditional(isBankTold) {
    if (isBankTold) {
      this.$bankTold.css('display', 'flex');
      this.$bankTold.find('.o-form__field , .o-form__field--additional').each((i, field) => {
        $(field).css('display', 'flex');
      })
    } else {
      this.$bankTold.css('display', 'none');
    }
  }
  getPossibleFormFields() {
    return {
      complaint: [
        'name',
        'emirate',
        'email',
        'country-prefix',
        'telephone',
        'accountNum',
        'accountTitle',
        'accountClassification',
        'accountBranch',
        'suggestions',
        'specifics',
        'toldBank',
        'g-recaptcha-response'
      ],
      contact: [
        'name',
        'country',
        'email',
        'country-prefix',
        'telephone',
        'contactSubject',
        'specifics',
        'g-recaptcha-response'
      ],
      shareholder: [
        'name',
        'emirate',
        'email',
        'country-prefix',
        'telephone',
        'accountNum',
        'accountTitle',
        'accountBranch',
        'suggestions',
        'specifics',
        'toldBank',
        'g-recaptcha-response'
      ],
      suggestion: [
        'name',
        'emirate',
        'email',
        'country-prefix',
        'telephone',
        'accountNum',
        'accountTitle',
        'accountClassification',
        'accountBranch',
        'suggestions',
        'g-recaptcha-response'
      ],
      commendation: [
        'name',
        'emirate',
        'email',
        'country-prefix',
        'telephone',
        'accountNum',
        'accountTitle',
        'accountClassification',
        'accountBranch',
        'suggestions',
        'g-recaptcha-response'
      ]
    }
  }
}
