import { dimmerToggle } from './helpers.js';

const filterizr = require("filterizr-rtl/dist/jquery.filterizr.min");

export class FilterDropdown {
  constructor(selector) {
    this.$dropdown = selector;
    this.$placeholder = this.$dropdown.find('.o-dropdown__placeholder');
    this.placeholderLocale = this.$placeholder.attr("data-locale");
    this.$placeholderText = this.$placeholder.text();
    this.selectedFilters = [];
  }

  init() {
    this.setProperties();
    this.addEventListeners();
  }

  setProperties() {
    this.$labelContainer = this.$dropdown.find('.o-dropdown__content');
    this.$filters = this.$labelContainer.find('.o-dropdown__filter');
    this.$filterButton = this.$labelContainer.find('.o-dropdown__filter-button');
    this.dropdownWidth = this.$dropdown.css('width');
    this.open = false;
  }

  toggleDropdown(isFilter = false) {

    const openClass = 'is-visible';
    this.open = !this.open;
    if (this.open) {
      dimmerToggle(true, true);
      this.$labelContainer.addClass(openClass);
      this.$placeholder.removeClass(openClass);
    } else {
      dimmerToggle(false, true);
      this.$labelContainer.removeClass(openClass);
      this.$placeholder.addClass(openClass);
      if (isFilter) {
        const placeholderText = this.selectedFilters.length > 0 ? `${this.selectedFilters.length} ${this.placeholderLocale}` : this.$placeholderText
        this.$placeholder.text(placeholderText);
      }
    }
  }

  addEventListeners() {
    this.$dropdown
      .off()
      .on('click', e => {
        e.preventDefault();
        e.stopPropagation();
        if (!this.open)
          this.toggleDropdown();
      }).on('touchend', e => {
        e.stopPropagation();
      });
    this.$filterButton
      .off()
      .on('click', e => {
        e.preventDefault();
        e.stopPropagation();
        this.handleFilter();
      })
      .on('touchend', e => {
        e.stopPropagation();
      })
    this.$filters.each((i, elem) => {
      const $filter = $(elem);
      $filter
        .off()
        .on('click', e => {
          e.preventDefault();
          e.stopPropagation();
          this.selectFilters($filter);
        })
        .on('touchend', e => {
          e.stopPropagation();
        })
    });
    $(document)
      .on('click', e => {
        if (this.open)
          this.toggleDropdown();
      })
      .on('touchend', e => {
        if (this.open)
          this.toggleDropdown();
      })
  }

  handleFilter() {
    this.toggleDropdown(true);
    this.filterCards();
  }

  selectFilters($filter) {
    this.toggleCheck($filter);
  }

  controlSelectedFilters($filter, isChecked) {
    const inputValue = $filter.data()['multifilter'];
    if (isChecked) {
      this.selectedFilters.push(inputValue);
    } else {
      const filterIndex = this.selectedFilters.indexOf(inputValue);
      this.selectedFilters.splice(filterIndex, 1);
    }
  }

  toggleCheck($filter) {
    const
      $checkbox = $filter.find('input'),
      $checkMark = $filter.find('.o-dropdown__checkmark'),
      isChecked = $checkbox.is(':checked'),
      innerChecked = '<span class="is-checked"></span>',
      newCheckedState = !isChecked;

    this.controlSelectedFilters($filter, newCheckedState);

    if (!isChecked) {
      $checkbox.prop("checked", true);
      $checkbox.addClass('is-checked');
      $checkMark.append(innerChecked);
    } else {
      const checkMark = $checkMark.find('is-checked')[0];
      $checkbox.prop("checked", false);
      $checkbox.removeClass('is-checked');
      $checkMark.empty();
    }
  }

  resetFilterzr() {
    this.$filterContent = $('.js-filter-content').filterizr({
      setupControls: true,
      layout: 'sameHeight',
      delay: 10,
      animationDuration: 0.5,
      easing: 'ease-out',
      multifilterLogicalOperator: 'or'
    });
  }

  filterCards() {

    this.resetFilterzr();
    if (this.$filterContent.length && !$('body').hasClass('fabricator')) {
      this.selectedFilters.forEach(filter => {
        this.$filterContent.filterizr('toggleFilter', filter);
      })
    }
    this.$filterContent = {};
  }

}
