export class Modal {
  constructor($selector) {
    this.$selector = $selector;
    this.$modal = this.$selector.find('.c-modal__wrapper');
    this.$close = this.$selector.find('.icon-close');
    this.$content = this.$selector.find('.c-modal__content');
    this.isOpen = false;
  }

  init() {
    this.addEventListeners();
  }

  addEventListeners() {
    this.$close
      .on('click', e => {
        e.stopPropagation();
        this.closeModal();
      })
    this.$selector
      .on('click', e => {
        e.stopPropagation();
        this.closeModal();
      });

    this.$modal
      .on('click', e => {
        e.stopPropagation();
      })
  }

  appendContent($content) {
    this.$content.empty();
    this.$content.append($content);
  }

  closeModal() {
    this.$selector.removeClass('open');
    $('body').removeClass('open-modal');
    this.isOpen = true;
  }

  openModal($content) {
    if($content) {
      this.appendContent($content);
      $content.show();
    } 
    this.$selector.addClass('open');
    $('body').addClass('open-modal');
    this.isOpen = false;
  }

}
