export class OtpValidation {
  constructor(otpForm) {
    this.$otpForm = $(otpForm);
    this.$otpBox = this.$otpForm.find('.c-calculator__input-text-box.-otp-text');
    this.$btnDone = this.$otpForm.find('.btn-done');
  }
  init() {
    this.addEventListeners();
  }
  addEventListeners() {
    this.$otpBox.on('input', (e) => {
      let minlength = parseInt($(e.target).data("minlength"));
      if ($(e.target).val().length >= minlength){
        this.$btnDone.attr('disabled', false);
      }
      else {
        this.$btnDone.attr('disabled', 'disabled');
      }
      
    }).on('keydown', (e) => {
      let maxlength = parseInt($(e.target).data("maxlength"));
      if (e.keyCode == 8 || e.keyCode == 46)  {
      }
      else {
        if ($(e.target).val().length >= maxlength){
          e.preventDefault();
        }
      }
    });
  }
}
