export class IbanForm {
  constructor($form) {
    this.$form = $form;
    this.$userInput = this.$form.find('.js-iban');
    this.$ibanPlaceholder = this.$form.find('.iban-placeholder');
    this.$ibanGenerateButton = this.$form.find(".c-iban__buttons button");
    this.$tooltip = this.$form.find('.c-iban__tooltip');
    this.$copy = this.$form.find('.c-input-copy');
  }

  init() {
    this.addEventListeners();
  }

  addEventListeners() {
    this.$userInput
      .on('keydown', e => {
        this.checkIsNumber(e);
      })
      .on('keyup', e => {
        this.checkLength();
      })
    this.$ibanGenerateButton.on('click', e => {
      e.preventDefault();
      e.stopPropagation();
      this.generateIban()
    })
  }

  checkLength() {
    const userInput = this.$userInput.val();

    if (userInput.length === 12 || userInput.length === 14) {
      this.$ibanGenerateButton.prop('disabled', false);
    } else {
      this.resetFields();
      this.$ibanGenerateButton.prop('disabled', true);
    }
  }

  resetFields() {
    this.$ibanPlaceholder.val('');
    this.$tooltip.css({
      'display': 'none'
    });
    this.$copy.css({
      'display': 'none'
    });
  }

  checkIsNumber(e) {
    // Allow: backspace, delete, tab, escape, enter and .
    if ($.inArray(e.keyCode, [46, 8, 9, 27, 13, 110, 190]) !== -1 ||
      // Allow: Ctrl/cmd+A
      (e.keyCode == 65 && (e.ctrlKey === true || e.metaKey === true)) ||
      // Allow: Ctrl/cmd+C
      (e.keyCode == 67 && (e.ctrlKey === true || e.metaKey === true)) ||
      // Allow: Ctrl/cmd+X
      (e.keyCode == 88 && (e.ctrlKey === true || e.metaKey === true)) ||
      // Allow: home, end, left, right
      (e.keyCode >= 35 && e.keyCode <= 39)) {
      // let it happen, don't do anything
      return;
    }

    // Ensure that it is a number and stop the keypress
    if ((e.shiftKey || (e.keyCode < 48 || e.keyCode > 57)) && (e.keyCode < 96 || e.keyCode > 105)) {
      e.preventDefault();
    }
  }

  generateIban() {
    const
      countryCode = "AE",
      countryCodeEq = "1014",
      bankCode = "003";

    let
      userInput = this.$userInput.val(),
      IBANNo;

      var inputlength = userInput.length;

      for(var h=0; h<16-inputlength; h++){
        userInput = "0" + userInput;
      }


    let tempIBAN = bankCode + userInput + countryCodeEq + "00";

    tempIBAN = tempIBAN.substring(0, 9) % 97 + "" + tempIBAN.substr(9);
    tempIBAN = tempIBAN.substring(0, 9) % 97 + "" + tempIBAN.substr(9);
    tempIBAN = tempIBAN.substring(0, 9) % 97 + "" + tempIBAN.substr(9);

    const mod97 = tempIBAN.substring(0, 9) % 97;

    let checkDigit = 98 - mod97;

    if (checkDigit / 10 < 1) {
      checkDigit = "0" + checkDigit;
    }

    IBANNo = countryCode + checkDigit + bankCode + userInput;

    this.appendToInput(IBANNo)
  }

  appendToInput(iban) {
    this.$ibanPlaceholder.val(iban);
    this.$copy.css({
      'display': 'block'
    });
    this.$copy
      .on('click', e => {
        e.preventDefault();
        this.copyToClipboard();
      })
  }
  copyToClipboard() {
    this.$ibanPlaceholder.focus();
    this.$ibanPlaceholder.select();
    document.execCommand('copy');
    this.$tooltip.css({
      'display': 'block'
    });
  }
}
