/* ==============================================================================
Index
----------------------------------------
==============================================================================
 01 - imports
 02 - wrap previous js in a function
 03 - bsGridOverlay() bootstrap grid helper initialization
 04 - initializeLogo() initialization
 05 - mainNavToggle initialization
 06 - collapse component initialization
 07 - search component initialization
 08 - comp cards list filter component initialization
 09 - init comp accordion
 10 - init ways to bank
 11  - news-carousel
 12 - init Filter Dropdowns
 13 - init Call-back forms
 14 - init SearchFilterDropdown
 15 - init Store Location
 16 - init select inputs
 17 - initialize radio buttons - only in contact form
 18 - init write-us form
 19 - init offers form
 20 - init learn cards form
 21 - init header
 22 - init easy banking tabs
 24 - init ADCB object on runtime, not document ready
============================================================================== */

