/* 24 - init ADCB object on runtime, not document ready */

export const initCoreModule = () => {

  initAdcbObj();
  window[$] = $;
  require("owl.carousel/dist/owl.carousel.min");
  /* require jquery validation plugin */
  require("jquery-validation/dist/jquery.validate.min");
  /* require additional functions to validate custom regex */
  require("jquery-validation/dist/additional-methods.min.js");

  require("d3/dist/d3.min.js");

  require("jquery-parallax.js/parallax.min.js");

  $(document).ready(e => {
    //ADCB.initOfferCards();
    initializeGrid();
    /* 12 - init Filter Dropdowns*/
    initializeClass('.o-dropdown.filter', FilterDropdown);
    /* 13 - init Call-back forms*/
    initializeClass('.c-leads__form', CallBackForm);
    /* 14 - init SearchFilterDropdown*/
    initializeClass('.c-search-results__filter', SearchFilterDropdown);

    initializeClass('.c-financial-filter', FinancialFilter);
    /* 15 - init Store Location*/
    initializeMap();
    /* 16 - init select inputs */
    initializeClass('.c-input-select', SelectInput);
    /* 18 - init write-us form */
    initializeClass('.c-contact-form', WriteUsForm);
    /* 20 - init learn cards form */
    initializeClass('.c-learn-cards__form-wrapper', CardsForm);

    initializeClass('.o-tabs', ScrollableTabs);
    /* 22 - init easy banking tabs */
    initializeClass('.o-on-page-tabs', OnPageTabNav);
    /* 23 - init iban-form */
    initializeClass('.c-iban__form', IbanForm);
    /* 23 - init accordions */
    initializeClass('.js-acc-group', AccordionGroup);
    /* 24 - init apply now buttons */
    initializeClass('.hero-pdp__btn', ApplyButton);
    /* 24 - init apply now buttons */
    initializeClass('.js-scroll-btn', ApplyButton);
    /* 23 - init common tabs */
    initializeClass('.c-tab-nav__list', CommonTabs);
    /* 24 - init common tabs in section nav too */
    //initializeClass('.c-section-nav__list', CommonTabs);
    /* 25 - init chat section for all pages */
    initializeClass('.c-chat', ChatSection);
    /* 26 - init common tabs in section nav too */
    initializeClass('.c-discover-tab__wrapper', CommonTabs);
    /*27 - init NPS popover */
    initializeClass('.c-nps__popover', NpsPopover);
    /* 23 - init common tabs. The class c-tab-nav__list will be replaced with  .o-tabs__tab-nav-list*/
    //initializeClass('.o-tabs__tab-nav-list', CommonTabs);

    initializeClass('.js-collapsible-nav__list', CollapsibleSideNav);

    initializeClass('.js-mega-tabs', MegaTabs);

    initializeClass('.hero-pdp__content', StickyPdpHeader);

    initializeClass('.js-promotion-carousel', Carousel, 'promotions');

    initializeClass('.js-news-carousel', Carousel, 'news');

    initializeClass('.js-videos-related-carousel', Carousel, 'videos-related');

    initializeClass('.js-hero-carousel', HeroCarousel);

    initializeClass('.c-ways-to-bank', WaysToBank);

    initializeClass('.o-comp-collapse', CompCollapse);

    initializeClass('.js-event-gallery', EventGallery);

    initializeClass('.o-range-slider', RangeSlider);

    /* 21 - init header */
    initializeClass('.site-header', MainHeader);

    initializeClass('.c-cms-content', CmsContent);

    /* 22 - init - general classes for ellipse lines that are added on the current component */
    initializeClass('.o-ellipse-one-line', BoundedText, 1);

    initializeClass('.c-offer-card-list__item', OfferCard);

    initializeClass('.c-site-map__wrapper', MobileAccordion);

    initializeClass('.o-ellipse-two-lines', BoundedText, 2);

    initializeClass('.o-ellipse-three-lines', BoundedText, 3);

    initializeClass('.sms-link', SmsLink);

    initializeClass('.o-progress-bar', ProgressBar);

    initializeClass('.js-compare-carousel', Carousel, 'compare-carousel');

    initializeClass('.c-compare-carousel', CompareProductsSection);

    initializeClass('.hasmore-details', MoreData, ".company-news-item");

    initializeClass('.js-pagination', Pagination);

    initializeClass('.o-calculator-radio', CalculatorRadio);

    initializeClass('.js-calculator-base', BasicCalculator);

    initializeClass('.-tab-smooth-scroll', ScrollToTabButton);

    initializeClass('.js-events-carousel', Carousel, 'events');

    initializeClass('.js-photo-carousel', Carousel, 'photo-gallery');

    initializeClass('.c-gdpr-bar', GdprBar);

    initializeClass('.btn-popup', Popup);

    initializeClass('.-login-tab', LoginTabNav);

    initializeClass('.c-virtual-keyboard', VirtualKeyboard);

    initializeClass('.c-login__form', LoginFormValidation);

    initializeClass('.-js-show-popup', PopupLogin);

    initializeClass('#otp_modal', OtpValidation);

    if (isTouchDevice()) {
      initializeClass('body', TouchToClick);
    }
  });
}
