import {
  breakpoint
} from "./helpers";

export class ScrollableTabs {
  constructor($tabs) {
    this.$tabs = $tabs;
    this.$tabsNav = this.$tabs.find('.o-tabs__tab-nav');
    this.$tabsNavHolder = this.$tabs.find('.o-tabs__tab-nav__holder');
    this.$tabsNavWrapper = this.$tabs.find('.o-tabs__tab-nav__wrapper');
    this.$tabsNavList = this.$tabsNav.find('.o-tabs__tab-nav-list');
    this.$tabsNavLink = this.$tabsNav.find('.o-tabs__tab-nav-link');
    this.$tabsNavItems = this.$tabsNav.find('.o-tabs__tab-nav-item');
    this.$prevBtn = this.$tabsNavHolder.find('.nav-prev');
    this.$nextBtn = this.$tabsNavHolder.find('.nav-next');
    this.checkWidthAndScrollability();
    if (this.$tabs.hasClass('-js-exceedsPort')) {
      this.centerSelected();
    }
  }
  init() {
    this.addEventListeners();
  }
  addEventListeners() {
    $(this.$tabsNavHolder).scroll((e) => {
      this.setNavs(e.currentTarget);
    });
    $(window).resize(e => {
      this.checkWidthAndScrollability();
      if (this.$tabs.hasClass('-js-exceedsPort')) {
        this.centerSelected();
      }
    })
    this.$tabsNavItems.click((e) => {
      this.$tabsNavItems.removeClass('is-selected').find('a').removeClass('is-active');
      $(e.currentTarget).addClass('is-selected').find('a').addClass('is-active');
      if (this.$tabs.hasClass('-js-exceedsPort')) {
        this.centerSelected();
      }
    });
    this.$prevBtn.click((e) => {
      this.$tabsNavHolder.animate({
        scrollLeft: "-=100",
      }, 300);
    });
    this.$nextBtn.click((e) => {
      this.$tabsNavHolder.animate({
        scrollLeft: "+=100",
      }, 300);
    });
  }
  checkWidthAndScrollability() {
    let totalTabsWidth = this.getCumulativeWidth(this.$tabsNavItems);

    if (Math.round(totalTabsWidth) > Math.round(this.$tabsNavList.outerWidth(true))) {
      this.$tabs.addClass("-js-exceedsPort");
    }
    else {
      this.$tabs.removeClass("-js-exceedsPort");
    }
    this.setNavs(this.$tabsNavHolder);
  }
  getCumulativeWidth(items) {
    let totalWidth = 0;
    items.each((index, tabsNavItem) => {
      totalWidth += $(tabsNavItem).outerWidth(true);
    });
    return totalWidth;
  }
  centerSelected() {
    let totalWidthTillSelected = 0;
    let selectedCenter = 0;
    this.$tabsNavItems.each((index, tabsNavItem) => {
      totalWidthTillSelected += $(tabsNavItem).outerWidth(true);
      if ($(tabsNavItem).hasClass("is-selected")) {
        selectedCenter =  $(tabsNavItem).outerWidth(true) / 2;
        return false
      }
    });

    let componentCenter =  (this.$tabsNavHolder[0].getBoundingClientRect().right - this.$tabsNavHolder[0].getBoundingClientRect().left)/ 2;
    this.$tabsNavHolder.animate({
      scrollLeft: Math.round(totalWidthTillSelected - selectedCenter - componentCenter )
    }, 300);
  }
  setNavs(tabHolder) {
    window.requestAnimationFrame(() => {
      if ($(tabHolder).scrollLeft() <= 20) {
        this.$prevBtn.addClass('hide');
        this.$tabs.removeClass('-js-noLeft');
      }
      else {
        this.$prevBtn.removeClass('hide');
        this.$tabs.addClass('-js-noLeft');
      }
      if (($(tabHolder).scrollLeft() + $(tabHolder).outerWidth(true)) >= $(tabHolder)[0].scrollWidth - 20) {
        this.$nextBtn.addClass('hide');
        this.$tabs.removeClass('-js-noRight');
      }
      else {
        this.$nextBtn.removeClass('hide');
        this.$tabs.addClass('-js-noRight');
      }
    });
  }
}
