import Hammer from 'hammerjs';
import {throttle, getOs} from './helpers';


export class TouchToClick {
  constructor($element) {
    delete Hammer.defaults.cssProps.userSelect;
    this.hammerManager = new Hammer.Manager($element[0]);
    this.throttledActionCallback = throttle((e) => {
      e.target.click()
    }, 200);
  }

  init() {
    this.osname = getOs();
    this.setupHammerManager();
    this.setupDomFlag();
    this.addEventListeners();
  }

  setupDomFlag() {
    $(document.body).addClass('touch-enabled');
  }

  setupHammerManager() {
    this.hammerManager.add(new Hammer.Tap({event: 'tap'}));
    this.hammerManager.add(new Hammer.Tap({event: 'doubletap', taps: 2}));
    this.hammerManager.add(new Hammer.Press({event: 'press', time: 200}));
  }

  addEventListeners() {
    if(this.osname === 'iOS'){
      this.hammerManager.on('tap doubletap press', this.throttledActionCallback);
    }
  }
}
