/* 01 - imports */
export class AccordionGroup {
  constructor($accGroup) {
    this.$accGroup = $accGroup;
    this.$accordions = this.$accGroup.find('.js-acc-item');
    this.isSynced = this.$accGroup.data('sync') ? true : false;
  }
  init() {
    this.initAccordions();
  }

  initAccordions() {
    this.$accordions.each((i, acc) => {
      this.initSingleAcc($(acc), i);
    })
    this.$accordions.each((i,acc) => {
      this.evalAccordion($(acc));
      this.addSingleListeners($(acc));
    })
  }

  initSingleAcc($accordion , i) {
    $accordion.attr({ 'data-index': i });
  }

  evalAccordion($accordion) {
    if ($accordion.attr('data-state') !== 'expanded') {
      $accordion.attr({
        'data-state': 'collapsed'
      });
      this.toggleTitleClass(false, $accordion);
    } else {
      this.toggleTitleClass(true, $accordion);
      $accordion.find('.js-acc-content').slideDown('fast');
    }
  }

  toggleTitleClass(isExpanded, $accordion) {
    const $title = $accordion.find('.js-acc-title');
    if (isExpanded) {
      $title.removeClass('icon-add').addClass('icon-minus');
    } else {
      $title.removeClass('icon-minus').addClass('icon-add');
    }
  }

  expandContent($content) {
    const
      $accordion = $content.parent();

      $content.slideDown(700, () => {
        this.toggleTitleClass(true, $accordion);
      });

      $content.on('click', e => {
        e.stopPropagation();
      });
  }

  scrollToTitle($accordion) {

    const
      accOffset = $accordion.offset().top,
      headerHeight = $('.site-header').outerHeight(),
      scrollDistance  = accOffset - headerHeight;

    if (!$accordion.hasClass('js-no-scroll')) {
      $('html,body').animate({scrollTop : scrollDistance}, 600)
    }
  }

  handleExpanded($accordion = undefined) {
    const $expanded = this.$accGroup.find('[data-state="expanded"]');
    if ($expanded.length)  {
      const $prevContent = $expanded.find('.js-acc-content');

      if ($accordion) {
        const isAfter = Number($expanded.attr('data-index')) < Number($accordion.attr('data-index'));
          this.toggleTitleClass(false,$expanded);
          if(isAfter) {
            $prevContent.css({'display' : 'none'})
            this.expandAccordion($accordion);
          } else {
            $prevContent.slideUp({
              duration: 800,
              complete: () => this.expandAccordion($accordion)
            })
          }
      } else {
        $prevContent.slideUp('slow', () => {
          this.toggleTitleClass(false, $expanded);
        });
      }
      $expanded.attr({ 'data-state': 'collapsed' });
    } else if ($accordion) {
      this.expandAccordion($accordion);
    }
  }

  expandAccordion($accordion, prev = false) {
    const
      $content = $accordion.find('.js-acc-content');

    this.expandContent($content);

      if(!prev) {
        this.scrollToTitle($accordion);
      }

    $accordion.attr({
      'data-state': 'expanded'
    });
  }

  addSingleListeners($accordion) {
    const $title = $accordion.find('.js-acc-title');
    $title
      .on('click', e => {
        e.preventDefault();
        const
          $accGroup = $accordion.parent(),
          $expandedAcc = $accGroup.find('[data-state="expanded"]'),
          $content = $accordion.find('.js-acc-content'),
          isCollapsed = $accordion.attr('data-state') === 'collapsed';

        if (isCollapsed) {
          this.handleExpanded($accordion);
        } else {
          this.handleExpanded();
        }
      })

  }
}
