export class StickyPdpHeader {
  constructor($header) {
    this.$header = $header;
    this.$heroPdp = $('.hero-pdp');
    this.sticky = this.$header.offset().top;
    this.scrollY = $(document).scrollTop();
    this.isScrollDown = true;
    this.prevScrollY = 0;
    this.isScrolled = false;
  }

  init() {
    this.addEventListeners();
    this.evalWindowPosition();
  }

  addEventListeners() {
    $(window)
      .on('scroll', e => {

        if(!this.isScrolled) {
          this.isScrolled = true;
          this.handleScroll();
        }
      })
  }

  handleScroll() {
    this.evalScroll();
    if (this.isStickyHeader) {
      this.$header.addClass('sticky');
      this.$heroPdp.addClass('sticky');
      if(!this.isScrollDown) {
        this.$header.addClass('background');
      } else {
        this.$header.removeClass('background');
      }
    } else {
      this.$header.removeClass('sticky');
      this.$heroPdp.removeClass('sticky');
    }

    this.isScrolled = false;
  }

  evalWindowPosition() {
    if (this.scrollY > this.sticky) {
      $('.site-header').css({'opacity' : '0'});
      //setTimeout so main nav has sticky class transitions completed
      setTimeout(() => {
        this.$header.addClass('sticky');
        this.$heroPdp.addClass('sticky');
        $('.site-header').css({ 'opacity': '1' });
      },500);
    }
  }

  get isStickyHeader() {
    return this.scrollY > this.sticky;
  }

  evalScroll() {

    if(this.prevScrollY > this.scrollY) {
      this.isScrollDown = false;
    }else{
      this.isScrollDown = true;
    }

    this.prevScrollY = this.scrollY;
    this.scrollY = $(document).scrollTop();
  }
}
 