import { breakpoint } from "./helpers";

export class ApplyButton {
  constructor($button) {
    this.$button = $button;
    var buttonPath = $button.attr('href');
    if (buttonPath != "#") {
      this.$target = $(buttonPath);
    }
    //below change to handle calculator buttons
    //this.$heroContainer = this.$button.closest('.hero-pdp__content');
    this.$heroContainer = $("body").find('.hero-pdp .hero-pdp__content').length > 0 ? $('.hero-pdp .hero-pdp__content') : $('.c-fingoals-hero-pdp .c-fingoals-hero-pdp__content');
  }

  init() {
    this.addEventListeners();
  }

  addEventListeners() {
    this.$button
      .on('click', e => {
        e.preventDefault();
        this.handleClick();
      })
  }

  handleClick() {
    const 
      titleHeight = this.$heroContainer.hasClass('sticky') ? 0 : this.$heroContainer.outerHeight(true),
      header = $("body").find('.site-header').length > 0 ? $('.site-header') : $('.site-header-menuless'),
      headerHeight = $(header).outerHeight(),
      isSticky = this.$target.offset().top - headerHeight - titleHeight,
      isStatic = this.$target.offset().top + headerHeight - titleHeight;
      //this.$heroContainer1 = $("body").find('.hero-pdp .hero-pdp__content').length > 0 ? "yahoo" : "blah" ;
    let targetOffset = this.$heroContainer.hasClass('sticky') ? isSticky : isStatic;
    if(breakpoint() !== 'xl') targetOffset = isSticky;
 
    $('html, body').animate({
      scrollTop : targetOffset
    }, 1000, 'swing')
  }
}
