/* 01 - imports */
/* 01 - imports */
import {
  breakpoint
} from './helpers.js';

/* 02 - export ChatSection class */
export class ChatSection {
  constructor($chat) {
    this.$chat = $chat;
    this.$chatButton = this.$chat.find('.c-chat__link');
  }

  init() {
    this.addEventListeners();

    //Checking for the GdprBar component in order not to overlap
    if(!$('.c-gdpr-bar').hasClass('-hide')) {
      $('.c-chat').addClass('-hasgdpr');
    }
  }

  addEventListeners() {
    $(window).on('scroll', e => {
      this.loadChatSectionDisplay();
    })

    this.$chatButton
      .on('click', e => {
        e.preventDefault();
        e.stopPropagation();
        this.openModal();
      })
  }

  openModal() {
    if (!this.$modal) {
      this.$chat.after(`
          <div class="c-chat__modal is-visible">
            <span class='icon-close'></span>
            <span class='icon-close -minimize'></span>
          </div>
        `
      )
      this.$modal = $('body').find('.c-chat__modal');
      this.addModalListeners();
    } else {
      this.$modal.addClass('is-visible');
    }
    //$('body').find('.c-chat--position').hide();
    this.$chatButton.removeClass('is-visible');
  }

  addModalListeners() {

    this.$modal.find('.icon-close')
      .on('click', e => {
        this.closeModal();
      })
    this.$modal.find('.c-chat-modal')
      .on('click', e => {
        e.stopPropagation();
      })
  }

  closeModal() {
    this.$modal.removeClass('is-visible');
    this.$chatButton.addClass('is-visible');
    //$('body').find('.c-chat--position').show();
  }

  loadChatSectionDisplay() {
    var gdprHeight = 0;
    var scrollDistance = $(window).scrollTop() + $(window).height();
    var footerObject = $('footer').offset();
    var footerDistance = 0, siteContent = 0;
    if(footerObject && footerObject.top) {
      footerDistance = footerObject.top;
    }
    if (this.$chat.hasClass('-hasgdpr')) {
      gdprHeight = 70;
      if (breakpoint() == "xs" || breakpoint() == "sm") {
        gdprHeight = 210
      }
      footerDistance = footerObject.top + gdprHeight;
    }
    var siteContentObject = $('.site-content').offset();
    if(siteContentObject && siteContentObject.top) {
      siteContent = siteContentObject.top;
    }

    var topBottomSpace = 150;

    if (breakpoint() == "xs" || breakpoint() == "sm") {
      topBottomSpace = 100;
    }

    /*
      Remove is-visible and is-hidden classes
    */
    if (this.$chatButton.hasClass('is-visible')) {
      this.$chatButton.removeClass('is-visible');
    }

    if (this.$chatButton.hasClass('is-hidden')) {
      this.$chatButton.removeClass('is-hidden');
    }

    /*
      When user scrolls below the hero image
    */
    if (scrollDistance > siteContent + topBottomSpace && scrollDistance > $(window).height() + topBottomSpace) {
      this.$chatButton.addClass('is-visible');
    } else {
      this.$chatButton.addClass('is-hidden');
    }
    /*
      Case when the scrolls to the postion of the footer section
    */
    if (scrollDistance >= footerDistance) {
      if (!this.$chat.hasClass('c-chat--position')) {
        this.$chat.addClass('c-chat--position');
      }
    } else {
      if (this.$chat.hasClass('c-chat--position')) {
        this.$chat.removeClass('c-chat--position');
      }
    }
  }
}
