import {breakpoint} from './helpers';

export class Popup {
  constructor($elem) {
    this.$elem = $elem;
    this.$href = this.$elem.attr('href');
    this.$width = this.$elem.attr('data-popup-width') ? this.$elem.attr('data-popup-width') : $(window).width();
    this.$height = this.$elem.attr('data-popup-height') ? this.$elem.attr('data-popup-height') : $(window).height();
  }
  init() {
    this.addEventListeners();
  }
  addEventListeners() {
    this.$elem.on('click', e => {
      e.preventDefault();
      let biggerBreakpoints = [];//['sm', 'lg', 'xl'];
      let options = ''
      if (biggerBreakpoints.includes(breakpoint())) {
        options = `width=${this.$width},height=${this.$height},status=no,menubar=no`
      }
      window.open(this.$href, "_blank", options);
    })
  }
}
