import { isRtl } from "./helpers";

export class HeroCarousel {
  constructor($carousel){
    this.$carousel = $carousel;
    this.firstTimeLoad = 0;
    this.isRtl = isRtl();
    this.itemLength = this.$carousel.find('.hero-premium__item').length;
    this.isSingleVideo = this.$carousel.find('.hero-premium__item').find('video').length && this.itemLength === 1;
  }
  init(){
    this.initCarousel();
  }

  initCarousel() {
    this.$carousel.owlCarousel({
      rtl: this.isRtl,
      nav: true,
      navText: [
        "<div class='o-carousel-button-left icon-next'></div>",
        "<div class='o-carousel-button icon-next'></div>"
      ],
      items: 1,
      loop: this.isSingleVideo ? false : true,
      smartSpeed: 500,
      autoplayTimeout: 10000,
      animateOut: 'fadeOut',
      onTranslated: (property) => {
        this.animateItemProgress(property, this.$carousel);
      },
      onInitialized: (property) => {
        this.$carousel.find('.owl-dots .owl-dot').append(`<span class="progress"></span>`);
        // allow progress in each item
        this.animateItemProgress(property, this.$carousel);
      },
      onTranslate: (property) => {
        this.resetVideos();
      }
    });
  }

  animateItemProgress(property, $carousel){

    const
      autoPlayTimer = property.relatedTarget.settings.autoplayTimeout,
      $activeSlide = $carousel.find('.owl-item.active'),
      $activeNavItem = $carousel.find('.owl-dots .owl-dot.active'),
      $activeNavItemProgress = $activeNavItem.find('.progress'),
      $navItemProgress = $carousel.find('.owl-dots .owl-dot .progress');

    let slideTimer = autoPlayTimer;

    $navItemProgress.stop().css({ width: 0 });

    // get media as video if it is an html5 video
    const video = $carousel.find('.owl-item.active video')[0];
    const img = $carousel.find('.owl-item.active img')[0];

    if(this.isSingleVideo) {
      this.playSingleVideo();
    } else {
      if ($(video).length) {
        // if (this.firstTimeLoad < $navItemProgress.length) {
        if(video.buffered.length == 0) {
          video.load();
          video.onloadeddata = () => {
  
            slideTimer = video.duration * 1000;
            video.play();
            $(video).parent().find('.hero-premium__video-poster').fadeOut('slow');
            $activeNavItemProgress.animate({ width: '100%' }, (slideTimer), () => {
              $activeNavItemProgress.css({ width: 0 });
              $carousel.trigger('next.owl.carousel');
              if ($(video).length) {
                video.currentTime = 0;
                video.pause();
              }
              this.firstTimeLoad += 1;
            });
          }
        }
        else {
          slideTimer = video.duration * 1000;
          video.play();
          $(video).parent().find('.hero-premium__video-poster').fadeOut('slow');
          $activeNavItemProgress.animate({ width: '100%' }, (slideTimer), () => {
            $activeNavItemProgress.css({ width: 0 });
            $carousel.trigger('next.owl.carousel');
            if ($(video).length) {
              video.currentTime = 0;
              video.pause();
            }
            slideTimer = 0;
          });
        }
      }
      else if ($(img).length) {
        slideTimer = ($(img).attr("data-duration")) ? $(img).attr("data-duration") * 1000 : 5000;
        $activeNavItemProgress.animate({ width: '100%' }, (slideTimer), () => {
          $activeNavItemProgress.css({ width: 0 });
          $carousel.trigger('next.owl.carousel');
        });
      }
    }
  }

  playSingleVideo() {
    this.$carousel.find('.hero-premium__item').find('video').attr('loop', 'loop');
    const singleVideo = this.$carousel.find('.hero-premium__item').find('video')[0];
    singleVideo.load();
    singleVideo.onloadeddata = () => {
      $(singleVideo).parent().find('.hero-premium__video-poster').fadeOut('slow');
      singleVideo.play();
    }
  }

  resetVideos() {
    const $videos = $('.owl-item video');
    $videos.each((i, video) => {
      video.pause()
      video.currentTime = 0;
    })
  }
}
