export class CalculatorRadio {
  constructor(elemnt) {
    this.$calculator_radio = $(elemnt);
    this.$calculator_radio_labels = this.$calculator_radio.find('.o-calculator-radio__label');
    this.$calculator_radio_inputs = this.$calculator_radio_labels.find('input[type="radio"]');
  }
  init() {
    this.onLoad();
    this.addEventListeners();
  }
  onLoad() {
    $(this.$calculator_radio.find("input[type='radio']:checked")).parents('.o-calculator-radio__label').addClass('-selected');;
  }
  addEventListeners() {
    this.$calculator_radio_inputs.on('change', e => {
      this.$calculator_radio_labels.removeClass('-selected');
      $(e.target).parents('.o-calculator-radio__label').addClass('-selected');
    });
  }
}
