import { isRtl } from './helpers.js';

export class RangeSlider {
  constructor(slide) {
    this.$slider = slide;
    this.$input = this.$slider.find('.slider');
    this.$displayValue = this.$slider.find('.o-range-slider__value');
  }

  init() {
    this.addEventListeners();
    this.displayInput();
    this.displayRange();
  }
  addEventListeners() {
    this.$slider
      .on('input change', e => {
        const value = e.target.value;
        this.$displayValue.val(value);
        this.$input.val(value);
        this.displayInput(this.replaceCommas(value.toString()));
        //alert(value);
        //console.log(value);
        this.$displayValue.trigger("propertyChange");
        //console.log(e);
        e.stopPropagation();
      })
    this.$displayValue
    .on('input', e => {
      //const value = e.target.value;
      let value = 0;
      if (e.target.value != "") {
        value = parseInt(e.target.value);
      }
      else {
        value = 0;
      }
      this.$input.val(this.replaceCommas(value.toString()));
      this.displayInput(this.replaceCommas(value.toString()));
      //this.$displayValue.trigger("propertyChange");
      let maxVal = parseInt(this.$input.attr("max"));
      if (value > maxVal) {
        value = maxVal;
      }
      this.$displayValue.val(value.toString());
      this.$input.val(this.replaceCommas(value.toString()));
      this.displayInput(this.replaceCommas(value.toString()));
      this.$displayValue.trigger("propertyChange");
    }).on('change', e => {
      let value = 0;
      if (e.target.value != "") {
        value = parseInt(e.target.value);
      }
      else {
        value = 0;
      }
      let minVal = parseInt(this.$input.attr("min"));
      let maxVal = parseInt(this.$input.attr("max"));
      if (value < minVal) {
        value = minVal;
      } 
      if (value > maxVal) {
        value = maxVal;
      }
      this.$displayValue.val(value.toString());
      this.$input.val(this.replaceCommas(value.toString()));
      this.displayInput(this.replaceCommas(value.toString()));
      this.$displayValue.trigger("propertyChange");
    }).on('focusin', e => {
      $(e.target).parents('.o-range-slider__value-holder').addClass('-focused');
      $(e.target).siblings('.o-tooltip__text').addClass('hide');
    })
    .on('focusout', e => {
      $(e.target).siblings('.o-tooltip__text').removeClass('hide');
    }).on('blur mouseout mouseover', e => {
        $(e.target).parents('.o-range-slider__value-holder').removeClass('-focused ');
    })
  }

  get getValue() {
    return this.$input.val()
  }

  displayInput(value = this.getValue) {

    this.$displayValue.value = this.$displayValue.attr('value', value);
    let gradient = this.normalizedGradient(value);
    const colorStopLeft = '#ec0100';
    const colorStopRight = '#f9afaf';

    if(isRtl()) {
      gradient = 1 - gradient;
      this.$input.css('background-image',
        '-webkit-gradient(linear, left top, right top, ' +
        'color-stop(' + gradient + ', ' + colorStopRight + '), ' +
        'color-stop(' + gradient + ', ' + colorStopLeft + ')' +
        ')'
      );
    }else {
      this.$input.css('background-image',
        '-webkit-gradient(linear, left top, right top, ' +
        'color-stop(' + gradient + ', ' + colorStopLeft + '), ' +
        'color-stop(' + gradient + ', ' + colorStopRight + ')' +
        ')'
      );
    }
  }

  normalizedGradient(value) {
    const range = this.$input.attr('max') - this.$input.attr('min');
    return (value - this.$input.attr('min')) / range;
  }

  displayRange() {
    const
      $rangeLabels = this.$slider.find('.o-range-slider__label--range'),
      min = this.$input.attr('min'),
      max = this.$input.attr('max');
    $rangeLabels.first().html(this.setMillionsMask(min));
    $rangeLabels.last().html(this.setMillionsMask(max));

  }

  setMillionsMask(value) {
    let size, decimals;

    value = value.toString();
    if (value.includes(".")) {
      decimals = (value + "").split(".");
      size = decimals[0].length - 3;
      while (size > 0) {
        decimals[0] = [decimals[0].slice(0, size), ',', decimals[0].slice(size)].join('');
        size = size - 3;
      }
      this.displayNumber(decimals[0] + "," + decimals[1], $target);
    } else {
      size = value.length - 3;

      while (size > 0) {
        value = [value.slice(0, size), ',', value.slice(size)].join('');
        size = size - 3;
      }
    }
    return value;
  }
  replaceCommas(money) {
    while (money.includes(','))
      money = money.replace(',', '')
    return money;
  }
}
