/* 01 - imports */
import {
  breakpoint
} from './helpers.js';

/* 02 - export CollapsibleSideNav class */
export class CollapsibleSideNav {
  constructor($tabs) {
    this.$tabs = $tabs;
    this.$tabsParent = this.$tabs.parent();
    this.$tabsSelectLink = this.$tabsParent.find('.js-selected-link');
    this.$tabNavListItemLink = $tabs.find('.js-nav-item a');
    this.$activeTab = this.getActiveTab();
    this.generatedTab = false;
    this.sitePaddingSmall = 24;
  }

  init() {
    this.tabsWidth = this.getTotalWidth();
    this.updateButtonText();
    //this.checkSizeAndGenerateTab();
    this.addEventListeners();
    this.scrollToActiveTab();
  }

  addEventListeners() {
    //$(window).on("resize", e => this.checkSizeAndGenerateTab());
    $(document).on('click', e => {
      if (this.$tabs.hasClass('is-expanded')) {
        this.$tabs.removeClass('is-expanded');
      }
    });

    this.$tabNavListItemLink.on('click', e => {
      //this.$tabs.find('.is-selected').removeClass('is-selected').find('.is-active').removeClass('is-active');
      //$(e.target).parents('.js-nav-sub-nav').siblings('a').css('color','red');
      if (this.$tabs.find('.is-active').parents('.js-nav-sub-nav').length) {
        /*for multi-level navs like the one on the FATCA page*/
        this.$tabs.find('.js-nav-sub-nav .is-active').removeClass('is-active');
        this.$tabs.find('.js-nav-sub-nav').siblings().removeClass('is-active');
        if ($(e.target).parents('.js-nav-sub-nav').length) {
          $(e.target).parents('.js-nav-sub-nav').siblings('a').addClass('is-active');
        }
      } else {
        this.$tabs.find('.is-active').removeClass('is-active');
        this.$tabs.find('.is-selected').removeClass('is-selected');
      }
      $(e.target).parent().addClass('is-selected');
      $(e.target).addClass('is-active');
      this.$activeTab = this.getActiveTab();
      this.updateButtonText();
      this.scrollToActiveTab();
    });

   this.$tabs.on('scroll', e => {
      let scrollLeftValue = this.$tabs.scrollLeft();

      if(scrollLeftValue === 0 || scrollLeftValue > 70) {
        this.$tabs.removeClass('-show-left-shadow');
        this.$tabs.addClass('-show-right-shadow');
      }else {
        this.$tabs.addClass('-show-left-shadow');
        this.$tabs.removeClass('-show-right-shadow');
      }
    });
  }

  getTotalWidth() {
    let totalWidth = 0;
    this.$tabs.find('li').each((i, tab) => {
      totalWidth += $(tab).outerWidth(true);
    })

    return totalWidth;
  }
  getActiveTab() {
    return this.$tabs.find('.is-active').length > 0 ? this.$tabs.find('.is-active') : this.$tabs.find('.is-selected');
  }
  updateButtonText() {
    this.$tabsSelectLink.text(this.$tabs.find('.is-active').text());
  }

  checkSizeAndGenerateTab() {
    if (breakpoint() == "xs" || breakpoint() == "sm") {
      if (this.tabsWidth > ($(document).innerWidth() - this.sitePaddingSmall)) {
        this.$tabsParent.addClass('dropdown');
        if (!this.generatedTab) {
          this.openSelectOnCollapse();
        }
      } else {
        this.$tabsParent.removeClass('dropdown');
      }
    } else {
      this.$tabsParent.removeClass('dropdown');
    }
  }

  scrollCenter(elem) {
    var active = this.$tabs.find(elem)
    var activeWidth = active.width() / 2;
    var pos = active.position().left + activeWidth; //get left position of active li + center position
    var elpos = this.$tabs.scrollLeft(); // get current scroll position
    var elW = this.$tabs.width(); //get div width
    pos = pos + elpos - elW / 2; // for center position if you want adjust then change this

      this.$tabs.animate({
        scrollLeft: pos
      }, 500);
  };


  scrollToActiveTab() {

    this.scrollCenter(this.getActiveTab());

    let selectedIndex = this.getActiveTab().parent('li').index();
    let tabsCount = this.$tabsParent.find('li').length;

    if(this.tabsWidth > ($(document).innerWidth() - this.sitePaddingSmall)) {
      if(selectedIndex > 0) {
        this.$tabs.addClass('-show-left-shadow');
      }else {
        this.$tabs.removeClass('-show-left-shadow');
      }

      if(selectedIndex + 1 ===  tabsCount) {
        this.$tabs.addClass('-show-right-shadow');
        this.$tabs.removeClass('-show-left-shadow');
      }else {
        this.$tabs.removeClass('-show-right-shadow');
      }
    }
  }

  openSelectOnCollapse() {
    this.$tabsSelectLink.on('click', e => {
      this.$tabs.toggleClass('is-expanded');
      e.stopPropagation();
    })
    this.generatedTab = true;
  }
}
