export class WaysToBank {
  constructor($waysToBank) {
    this.$waysToBank = $waysToBank;
    this.$tab = $(".c-ways-to-bank__tab-list");
    this.$tabList = $(".c-ways-to-bank__tab-list").find(".c-ways-to-bank__tab-list-item");
    this.$tabContentList = $(".c-ways-to-bank__tab-content-list").eq(0)
      .find(".c-ways-to-bank__tab-content-item");
    this.$selectInput = $(".c-ways-to-bank").find("#filter-ways-select");
  }

  init() {
    this.addEventListeners();
    this.loadSelectedTab(0);
  }

  addEventListeners() {

    this.$tabList.each((i, elem) => {
      const $link = $(elem).find(".c-ways-to-bank__tab-list-link");
      $link.on('click', e => {
        e.preventDefault();
        e.stopPropagation();
        this.handleClick(e, elem);
      });
    })

    this.$selectInput.on("change", e => {
      e.preventDefault();
      e.stopPropagation();
      let selVal = this.$selectInput.val();
      let selectedIndex = $('#dd-filter-ways-select option[value="' + selVal + '"]').index();
      this.loadSelectedTab(selectedIndex);
    });

  }

  loadSelectedTab(index) {
    $(".c-ways-to-bank__wrap").addClass("hidden").removeClass('is-active');
    $(".c-ways-to-bank__wrap")
      .eq(index)
      .removeClass("hidden").addClass('is-active');

    this.$tabContentList = $(".c-ways-to-bank__tab-content-list").eq(index)
      .find(".c-ways-to-bank__tab-content-item");

    $(".c-ways-to-bank__tab-content-list").removeClass("is-expanded");
    
    this.$tabContentList
      .addClass('prueba')
      .hide()
      .eq(0)
      .addClass('is-expanded')
      .fadeIn('slow');
  }

  loadSelectedFilterIndex() {

    if (this.$selectInput.length > 0) {
      let selVal = this.$selectInput.val();
      let selectedIndex = $('#dd-filter-ways-select option[value="' + selVal + '"]').index();
      return selectedIndex;
    }

    return 0;
  }

  handleClick(e, elem) {
    e.preventDefault();
    e.stopPropagation();
    const $target = $(e.target),
      $prevActive = this.$tabList.find(".ways-to-bank-active"),
      prevIndex = this.$tabList
      .find(".ways-to-bank-active")
      .parent()
      .index(),
      currIndex = $(elem).index();
    if (this.$tab.hasClass("is-expanded")) {
      this.$tab.removeClass("is-expanded");
    }
    $prevActive.removeClass("ways-to-bank-active");
    $target.find('.c-ways-to-bank__wrap is-active').addClass("ways-to-bank-active");

    this.$tabContentList
      .removeClass('is-expanded')
      .hide()
      .eq(currIndex)
      .addClass('is-expanded')
      .fadeIn('slow');
  }
}
