/* 01 - imports */

/* 02 - export ContainernumLines class */
export class BoundedText {
  constructor($container, numLines) {
    this.$container = $container;
    this.lineHeight = parseFloat(this.$container.css('line-height'));
    this.maxHeight = numLines * this.lineHeight;
    this.initialContent = this.$container.text();
  }

  init() {
    this.addEventListeners();
  }

  get containerHeight() {
    return this.$container.outerHeight();
  }

  addEventListeners() {
    $(window)
      .on("load", e => {
        this.applyTextChanges();
    });
  }

  applyTextChanges() {
    this.$container.html(this.getEllipseText());
  }

  getEllipseText(){
    const content = this.initialContent;

    if(!!content){
      return this.buildEllipseText(content);
    }
    return content;
  }

  buildEllipseText(content){

    let newContent = "", prevContent = "";
    let textSplit = " ";
    //if wordSplit == ""  split on each character
    //if wordSplit == " "  split on each space

    const contentArr = content.toString().split(textSplit);
    this.$container.html("");

    for(let h=0; h< contentArr.length; h++){

      prevContent = newContent;
      newContent = newContent + textSplit + contentArr[h];
      this.$container.html(newContent);

      if(this.containerHeight > this.maxHeight){
          newContent = prevContent.substring(0, prevContent.length - contentArr[h-1].length).trim();
          newContent = newContent + " ...";
          return newContent;
      }
    }

    return content;
  }
}
