/* 01 - imports */

/* 02 - export GdprBar class */
export class GdprBar {
  constructor($gdprBar) {
    this.$gdprBar = $gdprBar;
    this.$closeButton = this.$gdprBar.find(".close");
  }

  init() {
    this.addEventListeners();
  }

  addEventListeners() {

    this.$closeButton
      .on('click', e => {
        e.preventDefault();
        this.$gdprBar.addClass('-hide');
        $('.c-chat').removeClass('-hasgdpr');
      })
    }
}
