export class SelectInput {
  constructor(selector) {
    this.$select = selector;
    this.$dropdown = this.$select.find('.c-input-select__dropdown');
    this.$input = this.$select.find('.o-input');
    this.$valueHolder = this.$select.find('.value__holder');
    this.$arrow = this.$select.find('.icon-arrow-down');
    this.generateOptions()
    this.toggleDropdown.bind(this);
  }

  init() {
    this.addEventListeners();
  }

  get open() {
    return this.$select.hasClass('open');
  }

  addEventListeners() {
    this.$select
      .on('click', e => {
        this.resetAllDropdowns();
        e.preventDefault();
        e.stopPropagation();
        this.toggleDropdown(true);
      });
    
    this.$input
      .on('click', e => {
        e.preventDefault();
      })
      .on('keydown', e => {
        if (e.keyCode === 32 || e.keyCode === 40) {
          e.preventDefault();
          this.resetAllDropdowns();
          this.toggleDropdown();
        }
      })
    this.$optionSpans.each((i, option) => {
      $(option)
        .on('click', e => {
          e.preventDefault();
          e.stopPropagation();
          this.handleClick(e);
        })
        .on('focus', e => {
          this.handleFocus(e, $(option));
        })
        .off('keydown')
        .on('keydown', e => {
          this.handleKeyDown(e, $(option));

        })
    })

    $(document).on('click', e => {
      this.$select.removeClass('open');
    })
  }

  handleKeyDown(e, $option) {
    switch(e.keyCode) {
      case 32, 13:
        e.stopPropagation();
        e.preventDefault();
        this.handleClick(e);
        break;
      case 40:
        e.preventDefault();
        $option.next().focus();
        break;
      case 38:
        e.preventDefault();
        e.stopPropagation();
        if($option.is(':first-child')) {
          const $lastOption = (this.$optionSpans[this.$optionSpans.length - 1]);
          $lastOption.focus();
        } else {
          $option.prev().focus();
        }
        break;
      default:
        break;
    }
  }

  handleFocus(e, $option) {
    if($option.is(':last-child')) {
      const loopTab = e => {
        if (e.keyCode === 9 || e.keyCode === 40) {
          e.preventDefault();
          e.stopPropagation();
          this.resetOptionStyling();
          $(this.$optionSpans[0]).focus();
        }
      }
      $option
        .off('keydown', loopTab)
        .on('keydown', loopTab)
    }
  }

  toggleDropdown(isClick = false) {
    if (!this.open) {
      this.resetOptionStyling();
      this.$select.addClass('open');
      if(!isClick) {
        $(this.$optionSpans[0]).focus();
      }

    } else {
      this.$select.removeClass('open');
    }
  }

  resetAllDropdowns() {
    $(document).find('.c-input-select.open').each((i, dropdown) => {
      $(dropdown).removeClass('open');
    })
  }

  resetOptionStyling() {
    this.$optionSpans.each((i,option) => {
      const isSelected = this.$input.val() === $(option).html();
      if(isSelected) {
        $(option).addClass('hover');
      } else {
        $(option).removeClass('hover');
      }
    })
  }

  handleClick(e) {
    this.toggleDropdown();
    this.$input.addClass('-js-selected');
    this.$input.val(e.target.innerHTML);
    let value = ($(e.target).data('value') != undefined) ? $(e.target).data('value') : e.target.innerHTML;
    this.$valueHolder.val(value);
    //console.log(this.$valueHolder.val());
    this.$input.change();
    this.removeErrorClass();
  }

  removeErrorClass() {
    if(this.$input.hasClass('error')) {
      this.$input.removeClass('error');
    }
  }

  generateOptions() {
    this.$options = this.$select.find('option');
    if (this.$valueHolder.length == 0) {
      let inputName = this.$input.attr('name');
      this.$input.after(`<input readonly name='${inputName}__hide' class='value__holder hide' value='' />`);
      this.$valueHolder = this.$select.find('.value__holder');
    }
    this.$valueHolder.val("");
    this.$options.each((i, elem) => {
      let optionValue = $(elem).val();
      //console.log($(elem).data('displaytext'));
      let displaytext = ($(elem).data('displaytext') != undefined) ? $(elem).data('displaytext') : optionValue;
      this.$dropdown.append(`<span tabindex='0' class='c-input-select__option' data-value='${optionValue}'>${displaytext}</span>`)
    });
    this.$optionSpans = this.$select.find('.c-input-select__option');
  }
}
