import { breakpoint } from "./helpers";

export class ScrollToTabButton {
  constructor($tab) {
    this.$tab = $tab;
  }

  init() {
     this.addEventListeners();
  }

  addEventListeners() {
    this.$tab
      .on('click', e => {
        e.preventDefault();
        this.handleClick();
      })
  }

  handleClick() {
    $("html, body").animate({
       scrollTop: $(this.$tab.attr("href")).offset().top - 100
     }, 1000, 'swing');
  }
}
