import { evalCaptcha } from "./helpers";

export class CallBackForm {
  constructor(form) {
    this.$form = form;
    this.$submitButton = this.$form.find('button');
    this.$radioButtons = this.$form.find('.c-leads__radio-btn label')
    ADCB.initRecaptchaCallback();
  }

  init() {
    this.addEventListeners();
    this.handleSubmit();
  }

  handleSubmit() {
    this.validator = this.$form.validate({
      errorPlacement: (errorMap, errorList) => {
        // Do nothing here
      },
      submitHandler: (form) => {
        if (evalCaptcha(this.$form)) {
          form.submit();
        }
      }
    });
  }

  addEventListeners() {
    this.$radioButtons.each((i, elem) => {
      $(elem)
        .off()
        .on('click', e => {
          const $target = $(e.target);
          if ($target.parent().find('input').val() === 'no') {
            this.toggleCustomerId(false);
          } else {
            this.toggleCustomerId(true);
          }
        });
    })

  }

  toggleCustomerId(isCustomer) {
    const customerInput = this.$form.find('.customer-id-wrap');
    if (isCustomer) {
      customerInput.css('display', 'flex');
    } else {
      customerInput.css('display', 'none');
      customerInput.val('');
    }
    this.validator.resetForm();
  }
}
