export class PopupLogin {
  constructor(triggerClass) {
    this.$triggerClass = $(triggerClass);
    this.$modal = $('body').find('.c-modal');
  }
  init() {
    this.addEventListeners();
  }
  addEventListeners() {
    this.$modal.find('.icon-close')
      .on('click', e => {
        this.closeModal($(e.target).parents('.c-modal.open'));
      });
    this.$triggerClass.on('click', (e) => {
      let popupModalId = '#' + $(e.target).data('popup');
      this.openModal($('body').find(popupModalId));
      e.stopPropagation();
      e.preventDefault();
    })
  }
  openModal(modalSelector) {
    $('body').addClass('open-modal');
    $(modalSelector).addClass('open');
  }
  closeModal(modalSelector) {
    $(modalSelector).removeClass('open');
    $('body').removeClass('open-modal')
  }
}
