export class BsGridOverlay {
  constructor(cols) {
    this.cols = cols;
    this.gridActive = false;
    this.colsTemplate = '';
  }

  init() {
    this.addEventListeners();
    this.getColsTemplate();
  }

  getColsTemplate() {
    for (let i = 0; i < this.cols; i += 1) {
      this.colsTemplate += '<div class="col-xs-1"><div class="fill"></div></div>';
    }
  }

  addEventListeners() {
    document.addEventListener('keydown', e => {
      if (e.altKey && e.keyCode == 71 && this.gridActive == false) {
        $('body').append(this.gridTemplate);
        this.gridActive = true;
      } else {
          $('body').find('.bs-grid-overlay').remove();
        this.gridActive = false;
      }

    }, false);
  }

  get gridTemplate() {
    return (
      `
      <div class="bs-grid-overlay">
        <div class="container">
          <div class="row">${this.colsTemplate}</div>
        </div>
      </div>
      `
    )
  }
}
