import { breakpoint } from './helpers.js';

export class MobileAccordion {
    constructor($selector) {
      this.$parent = $selector;
      this.$sections = this.$parent.find('.js-acc-section');
      this.windowWidth = $(window).width();
    }

    get isMobile() {
      return breakpoint() === 'xs';
    }

    init() {
      this.addEventListeners();
      this.evalAccordion();
    }

    addEventListeners() {
      $(window)
        .on('resize', e => {
          if($(window).width() !== this.windowWidth) {
            this.windowWidth = $(window).width();
            this.evalAccordion();
          }
        })
    }

    evalAccordion() {
      if(this.isMobile && !this.isAccordion) {
        this.createAccordion();
      } else if (!this.isMobile && this.isAccordion) {
        this.resetAccordion();
      }
    }

    createAccordion() {
      this.isAccordion = true;
      this.$sections.each((i,section) => {
        this.handleSection(section);
      })
    }

    resetAccordion() {
      this.isAccordion = false;
      this.$sections.each((i,section) => {
        this.resetSection(section);
      })
    }

    resetSection(section) {
      const 
        $title = $(section).find('.js-acc-title'),
        $content = $(section).find('.js-acc-content');
      $title.off();
      $content.css({ 'display': 'block' });
    }

    handleSection(section) {
      const 
        $section = $(section),
        $title = $section.find('.js-acc-title'),
        $content = $section.find('.js-acc-content'),
        $icon = $section.find('.icon-minus');

      $title.attr({
        'data-originalPos': $title.offset().top - $title.outerHeight(true)
      })

      if($icon) {
        $icon.removeClass('icon-minus').addClass('icon-add');
      }

      this.addSingleListeners($title, $content);
      $content.css({ 'display': 'none' });
    }

    addSingleListeners($title, $content) {
      $title
        .on('click', e => {
          e.preventDefault();
          e.stopPropagation();
          this.handleClick($content);
        })
    }

    scrollToTitle($title) {
      const titleHeight = Number($title.attr('data-originalpos'));
      $('html, body').animate({scrollTop : titleHeight}, 600)
    }

    handleClick($content) {
      const $title = $content.prev();
      if ($content.is(':visible')) {
        const $icon = $content.prev().find('.icon-minus');
        if ($icon) $icon.removeClass('icon-minus').addClass('icon-add');
        $content.css({'display': 'none'});
      } else {
        this.resetExpanded();
        const $icon = $title.find('.icon-add');
        $content.slideDown(800);
        if($icon) $icon.removeClass('icon-add').addClass('icon-minus');
      }
      this.scrollToTitle($title);
    }

    resetExpanded() {
      this.$sections.each((i,section) => {
        const $content = $(section).children().eq(1);
        if($content.is(':visible')) {
          $content.slideUp(600);
          const $icon = $content.prev().find('.icon-minus');
          if ($icon) $icon.removeClass('icon-minus').addClass('icon-add');
        }
      })
    }
}