    this.$calcStageNavigationLevel2 = this.$calcStage.find('.c-calculator-stage__navigation.-level-2');
    this.$calcStagePrevLevel2 = this.$calcStageNavigationLevel2.find('.nav-prev');
    this.$calcStageNextLevel2 = this.$calcStageNavigationLevel2.find('.nav-next');
    this.$calcStageNavigationLevel3 = this.$calcStage.find('.c-calculator-stage__navigation.-level-3');
    this.$calcStagePrevLevel3 = this.$calcStageNavigationLevel3.find('.nav-prev');
    this.$calcStageRestartLevel3 = this.$calcStageNavigationLevel3.find('.nav-next');

    this.$financialGoalsCategoriesHolder = this.$finGoals.find('.c-financial-goals__categories');
