  enableDisableBoxes (e) {
    if($(e).val().length < 3) {
      $(e).parents('.js-calculator-overlay__input').addClass('disabled');
      $(e).parents('.js-calculator-overlay__input').find("input.slider").attr('disabled', 'disabled');
      $(e).parents('.js-calculator-overlay__input').find("input.o-range-slider__value").attr('disabled', 'disabled');
      $(e).parents('.js-calculator-overlay__input').find(".c-calculator__apply-button-holder .o-btn-medium-red").attr('disabled', 'disabled');
    }
    else {
      $(e).parents('.js-calculator-overlay__input').removeClass('disabled');
      $(e).parents('.js-calculator-overlay__input').find("input.slider").removeAttr('disabled');
      $(e).parents('.js-calculator-overlay__input').find("input.o-range-slider__value").removeAttr('disabled');
      $(e).parents('.js-calculator-overlay__input').find(".c-calculator__apply-button-holder .o-btn-medium-red").removeAttr('disabled');
    }; 
  }
