import {
  dimmerToggle
} from "./helpers";

export class OfferCard {
  constructor($card) {
    this.$card = $card;
    this.$siteWrap = $('.site-wrap');
    this.$background = this.$card.parent().find('.c-offer-detail__background')
    this.$offerDetailWrapper = this.$card.parent().find('.c-offer-detail__wrapper');

    this.$offerDetailCloseButton = this.$offerDetailWrapper.find('.c-offer-detail__close');
    this.$jsDimmerClass = '.js-dimmer';
  }
  init() {
    //is-anchored is added as a modifier Class inorder to have the Default behavior of the anchor class
    if(!$('.c-offer-cards__list').hasClass('is-anchored')) {
      this.addEventListeners();
    }
  }
  addEventListeners() {
    this.$card
      .on('click', e => {
        e.preventDefault();
        e.stopPropagation();
        this.toggleOfferDetails();
      });
    $(document).on('click', e => {
      if ($(e.target).parents('.c-offer-detail__wrapper').length <= 0) {
        this.toggleOfferDetails(true);
      }
    })
    this.$offerDetailCloseButton
      .on('click', e => {
        this.toggleOfferDetails(true);
      })
    this.$offerDetailWrapper.on('click', e => {
      e.preventDefault();
      e.stopPropagation();
    });
  }

  toggleOfferDetails(reset = false) {
    const
      isExpanded = !reset ? this.$offerDetailWrapper.hasClass('is-expanded') : true
    var $dimmerToggle = undefined;
    if (isExpanded) {
      $('body').removeClass('no-scroll');
      this.$background.css({
        'visibility': 'hidden'
      });
      this.$offerDetailWrapper.hide();
      this.$offerDetailWrapper.removeClass('is-expanded');
      $(this.$jsDimmerClass).removeClass('js-dimmer-over-head');
    } else {
      this.$background.css({
        'visibility': 'visible'
      });
      this.$offerDetailWrapper.show();
      $('body').addClass('no-scroll');
      this.$offerDetailWrapper.addClass('is-expanded');
      $(this.$jsDimmerClass).addClass('js-dimmer-over-head');
    }
  }
}
