import { breakpoint } from "./helpers";

export class LoginTabNav {
    constructor($tabs) {
        this.$tabs = $tabs.find('.c-tab-nav');
        // tab link items
        this.$tabsNav = $tabs.find('.c-tab-nav__wrapper');
        this.$tabsNavLink = this.$tabsNav.find('.c-tab-nav__link');
        // tab content items
        this.$tabsParent    = $tabs.find('.c-login-tab-content');
        this.$tabsContent   = $tabs.find('.c-login-tab-content__item');
        let dataID = this.getActiveTabContentId();
        this.showContent(dataID);
    }

    init() {
        this.addEventListeners();
    }

    addEventListeners() {
        this.$tabsNavLink
            .on('click', e => {
                e.stopPropagation();
                e.preventDefault();
                const
                    $target     =   $(e.target),
                    $parent     =   $target.closest("li"),
                    $parentId   =   $parent.attr('data-lg-id');
                    // Show current tab and Hide all other tabs
                    this.hideContent();
                    this.showContent($parentId);
            })
    }

    // Display current active tab content
    showContent(dataID) {
        this.$tabsParent.find("[id='"+ dataID +"']").addClass('is-active');
    }

    // Hide all tab content
    hideContent() {
        this.$tabsParent.find('.is-active').removeClass('is-active');
    }

    // Get current active tab Id
    getActiveTabContentId() {
        return this.$tabsNavLink.filter('.is-active').parent().attr('data-lg-id');
    }
}
