

export class TriggerSelect {
  constructor(selector) {
    this.$select = selector;
    this.$dropdown = this.$select.find('.c-input-select__dropdown');
    this.$input = this.$select.find('.o-input');
    this.$arrow = this.$select.find('.icon-arrow-down');
    this.generateOptions()
    this.toggleDropdown.bind(this);
  }

  init() {
    if(!this.$input.prop('disabled'))
      this.addEventListeners();
  }

  get open() {
    return this.$select.hasClass('open');
  }
  addEventListeners() {
    this.$select
      .on('click', e => {
        this.resetAllDropdowns();
        e.preventDefault();
        e.stopPropagation();
        this.toggleDropdown(true);
      });

    this.$input
      .on('click', e => {
        e.preventDefault();
        e.stopPropagation();
      })
      .on('keydown', e => {
        if (e.keyCode === 32 || e.keyCode === 40) {
          e.preventDefault();
          this.resetAllDropdowns();
          this.toggleDropdown();
        }
      })
    this.$optionSpans.each((i, option) => {
      $(option)
        .on('click', e => {
          e.preventDefault();
          e.stopPropagation();

          const selectedValues = this.getOfferInputs();
          const parentInput = $(option).closest('.c-input-select--trigger').find('input').attr('name');
          selectedValues[parentInput] = $(option).html();
          this.triggerRequest(selectedValues);
        })
        .on('focus', e => {
          this.handleFocus(e, $(option));
        })
        .off('keydown')
        .on('keydown', e => {
          this.handleKeyDown(e, $(option));

        })
    })

    $(document).on('click', e => {
      this.$select.removeClass('open');
    })
  }

  initSelectOptions(){
    const
      $selectOptions = $('.c-input-select__option'),
      selectedValues = this.getOfferInputs();

    $selectOptions.each((i, option) => {
      $(option)
        .on('click', e => {
          const parentInput = $(option).closest('.c-input-select--trigger').find('input').attr('name');
          selectedValues[parentInput] = $(option).html();
          this.triggerRequest(selectedValues);
        })
    })
  }

  triggerRequest(selectedValues){
    const
      keys = Object.keys(selectedValues),
      values = [],
      getValues = keys.forEach(key => {
        values.push(`${key}=${selectedValues[key]}`);
      }),
      URI = encodeURI(values.join().replace(/,/g, '&')),
      URL = String(window.location.href),
      cleanURL = URL.substring(0, URL.indexOf('?'));

    setTimeout(() => {
      window.location.href = `${cleanURL}?${URI}`;
    }, 10)
    return false;
  }

  getOfferInputs(){
    const
      URIvalues = {},
      $inputs = $('.c-input-select--trigger').each((i, elem) => {
        const
          $input = $(elem).find('input'),
          name = $input.attr('name');

        URIvalues[name] = $input.val();
      });

    return URIvalues;

  }

  handleKeyDown(e, $option) {
    switch (e.keyCode) {
      case 32, 13:
        e.stopPropagation();
        e.preventDefault();
        const selectedValues = this.getOfferInputs();
        const parentInput = $option.closest('.c-input-select--trigger').find('input').attr('name');
        selectedValues[parentInput] = $option.html();
        this.triggerRequest(selectedValues);        break;
      case 40:
        e.preventDefault();
        $option.next().focus();
        break;
      case 38:
        e.preventDefault();
        e.stopPropagation();
        if ($option.is(':first-child')) {
          const $lastOption = (this.$optionSpans[this.$optionSpans.length - 1]);
          $lastOption.focus();
        } else {
          $option.prev().focus();
        }
        break;
      default:
        break;
    }
  }

  handleFocus(e, $option) {
    if ($option.is(':last-child')) {
      const loopTab = e => {
        if (e.keyCode === 9 || e.keyCode === 40) {
          e.preventDefault();
          e.stopPropagation();
          this.resetOptionStyling();
          $(this.$optionSpans[0]).focus();
        }
      }
      $option
        .off('keydown', loopTab)
        .on('keydown', loopTab)
    }
  }

  toggleDropdown(isClick = false) {
    if (!this.open) {
      this.resetOptionStyling();
      this.$select.addClass('open');
      if(!isClick) {
        $(this.$optionSpans[0]).focus();
      }

    } else {
      this.$select.removeClass('open');
    }
  }

  resetAllDropdowns() {
    $(document).find('.c-input-select.open').each((i, dropdown) => {
      $(dropdown).removeClass('open');
    })
    $(document).find('.c-input-select--trigger.open').each((i, dropdown) => {
      $(dropdown).removeClass('open');
    })
  }

  resetOptionStyling() {
    this.$optionSpans.each((i, option) => {
      const isSelected = this.$input.val() === $(option).html();
      if (isSelected) {
        $(option).addClass('hover');
      } else {
        $(option).removeClass('hover');
      }
    })
  }

  generateOptions() {
    this.$options = this.$select.find('option');
    this.$options.each((i, elem) => {
      this.$dropdown.append(`<span tabindex='0' class='c-input-select__option'>${$(elem).val()}</span>`)
    });
    this.$optionSpans = this.$select.find('.c-input-select__option');
  }
}
