import { isMobile } from './helpers.js';

export class SingleBranch {
  constructor(branch, branchId) {
    this.branch = branch;
    this.branchId = branchId;
    this.htmlTemplate = this.generateHtml();
  }

  init() {
    this.addEventListeners()
  }



  addEventListeners() {
    const
      $branchCard = $(this.branchId),
      $iconSpan = $branchCard.find('.icon-add');

    $branchCard
      .on('click', e => {
        this.toggleClass($branchCard);
      })
    $branchCard.find('.c-branch-item__tel')
      .on('click', e => {
        if(!isMobile()) {
          e.preventDefault();
          this.copyToClipboard(e.target);
        }
      })
    $iconSpan
      .on('click', e => {
        this.handleIconClick($branchCard, e);
      })
  }

  handleIconClick($card, e) {
    const isActive = $card.hasClass('c-branch-item--active');
    if (isActive) {
      e.stopPropagation();
      this.resetCards();
    }
  }

  getBranchId() {
    return this.branchId.replace('#', '');
  }

  resetCards() {
    const activeCards = $('.c-branch-item--active');
    if (activeCards.length) {
      activeCards.each((i, card) => {
        $(card).removeClass('c-branch-item--active');
        $(card).addClass('c-branch-item');
      })
    }
  }

  toggleClass($card, isRemove = false) {
    const isActive = $card.hasClass('c-branch-item--active');
    if (!isActive) {
      this.resetCards()
      if(!isRemove) {
        $card.removeClass('c-branch-item');
        $card.addClass('c-branch-item--active');
      }
    }
  }

  get phoneNumber() {
    return this.branch.phone ? `href="tel:${this.branch.phone.replace(/\s+/g, '')}"` : '';
  }

  getContactInfo() {
    if(this.branch.branchtype !== 'ATM') {
      return (
        `<div class="c-branch-item__section">
            <span class="c-branch-item__subtitle">
              Contact Numbers
            </span>
            <a ${this.phoneNumber}" class="c-branch-item__tel">
              T: ${this.branch.phone ? this.branch.phone : ` ${window.ADCB.mapTranslations.notavailable} `}
            </a>
            <span>
              F: ${this.branch.fax ? this.branch.fax : ` ${window.ADCB.mapTranslations.notavailable} `}
            </span>
          </div>`
      )
    } else {
      return ``
    }
  }

  generateHtml() {
    return (
      `
        <div id='${this.getBranchId()}' class="c-branch-item">
        <div class="c-branch-item__title-wrapper">
          <h2 class="c-branch-item__title">${this.branch.branch}</h2>
          <span class="icon-add"></span>
        </div>
        <div class='c-branch-item__content'>
        <div class="c-branch-item__wrap">
          <div class="c-branch-item__section">
            <span class="c-branch-item__subtitle">${window.ADCB.mapTranslations.address}</span>
            <span data-infotype="location">${this.branch.location}</span>
            <span data-infotype='pobox'>${this.branch.pobox ? this.branch.pobox : ''}</span>
          </div>
          ${this.getContactInfo()}


          </div>
          <div class="c-branch-item__wrap--additional">
          <div class="c-branch-item__section">
            <span class="c-branch-item__subtitle">${window.ADCB.mapTranslations.services}</span>
            <span>${this.branch.facilities}</span>
          </div>
          <div class="c-branch-item__section">

            <span class="c-branch-item__subtitle">
              Timetable
            </span>
            <span>
            ${this.branch.timings ? this.branch.timings : ` ${window.ADCB.mapTranslations.notavailable} `}
            </span>
          </div>
          </div>
          <span class='c-branch-item__link'>${window.ADCB.mapTranslations.getdirection}</span>
          </div>
        </div>
        `
    )
  }
}
