import { breakpoint } from "./helpers";

export class OnPageTabNav {
    constructor($tabs) {
        this.$tabs = $tabs.find('.o-tabs-content__wrapper');
        this.$tabsNav = $tabs.find('.o-tabs__tab-nav');
        this.$tabsNavHolder = $tabs.find('.o-tabs__tab-nav__holder');
        this.$tabsNavLink = this.$tabsNav.find('.o-tabs__tab-nav-link');
        this.$tabsNavItems = this.$tabsNav.find('.o-tabs__tab-nav-item');
        this.setContent();
    }

    init() {
        this.addEventListeners();
    }

    addEventListeners() {
        this.$tabsNavLink
            .on('click', e => {
                e.stopPropagation();
                e.preventDefault();
                const
                    $target = $(e.target),
                    $parent = $target.parent(),
                    parentIndex = $parent.index();

                if (!$target.hasClass('is-active')) {
                    this.removeActive(this.$tabsNav);

                    this.removeActive(this.$content);
                    $target.addClass('is-active').parent().addClass('is-selected');
                    this.$contentItem.eq(parentIndex).addClass('is-active');
                }
                this.centerSelected();
            })
        this.$contentItem
            .on('click', e => {
                if($(window).innerWidth() <= 768) {
                    const moreLink = $(e.currentTarget).find('.more-link')[0];
                    $(moreLink)[0].click();
                }
            })
    }

    removeActive($target) {
        $target.find('.is-active').removeClass('is-active').parent().removeClass('is-selected');
    }
    centerSelected() {
        let totalWidthTillSelected = 0;
        let selectedCenter = 0;
        this.$tabsNavItems.each((index, tabsNavItem) => {
          totalWidthTillSelected += $(tabsNavItem).outerWidth(true);
          if ($(tabsNavItem).hasClass("is-selected")) {
            selectedCenter =  $(tabsNavItem).outerWidth(true) / 2;
            return false
          }
        });
    
        let componentCenter =  (this.$tabsNavHolder[0].getBoundingClientRect().right - this.$tabsNavHolder[0].getBoundingClientRect().left)/ 2;
        this.$tabsNavHolder.animate({
          scrollLeft: Math.round(totalWidthTillSelected - selectedCenter - componentCenter )
        }, 300);
      }
    setContent() {
        this.$content = this.$tabs.find('.o-tabs__tab-content');
        this.$contentItem = this.$tabs.find('.o-tabs__tab-content-item');
    }
}
