export const initRecaptchaCallback = () => {
  try {
    return window.recaptchaCallback = () => {
      const
        $captchaContainer = $('.js-captcha'),
        $errorMsg = $captchaContainer.find('span'),
        $label = $captchaContainer.find('label');

      if($captchaContainer.hasClass('error')) {
        $captchaContainer.removeClass('error');
      }
    }
  }
  catch (e) {
    alert(e);
  }
}

export const evalCaptcha = ($form) => {
  try {
    const
      $captcha = $form.find('#g-recaptcha-response'),
      isUnchecked = $captcha.val() === '',
      $captchaContainer = $('.js-captcha'),
      $errorMsg = $captchaContainer.find('span'),
      $label = $captchaContainer.find('label');

    if (isUnchecked) {
      $captchaContainer.addClass('error');
      return false;
    }
  }
  catch (e) {
    alert("Here", e );
    return true;
  }
  return true;
}

/* 02 - create overlay a transparent overlay for the body */
export const dimmerToggle = (show = true, isDropdown = false, pace = undefined ) => {

  const 
    $siteContent = $('.site-content'),
    $dimmer = isDropdown ? 
    `<div class="js-dimmer--dropdown"><div>` : 
    `<div class="js-dimmer"></div>`,
    dimmerClass = isDropdown ? '.js-dimmer--dropdown' : '.js-dimmer';
  var speed = isDropdown ? 'fast' : 'slow';
  speed = pace ? pace : speed;
  if (show === false) {
    $siteContent.find(dimmerClass).fadeOut(speed);
  } else {
    if ($siteContent.find(dimmerClass).length <= 0) {
      $siteContent.append($dimmer);
    }
    $siteContent.find(dimmerClass).fadeIn(speed);
  }
}

