/* 03 - responsive helpers returns string is-xs, is-sm, is-md, is-lg or is-xl */
export const breakpoint = () => {

  const 
    breakpoints = [0, 768, 992, 1232, 1440],
    viewportWidth = $(window).innerWidth();

  if (viewportWidth >= breakpoints[4]) {
    return "xl";
  }
  if (viewportWidth >= breakpoints[3]) {
    return "lg";
  }
  if (viewportWidth >= breakpoints[2]) {
    return "md";
  }
  if (viewportWidth >= breakpoints[1]) {
    return "sm";
  }
  if (viewportWidth >= breakpoints[0]) {
    return "xs";
  }

}


export const windowportSize = () => {

  const 
    breakpoints = [0, 768, 992, 1232, 1440],
    viewportWidth = $(window).innerWidth();

  if (viewportWidth >= breakpoints[4]) {
    return "xl";
  }
  if (viewportWidth >= breakpoints[3]) {
    return "lg";
  }
  if (viewportWidth >= breakpoints[2]) {
    return "md";
  }
  if (viewportWidth >= breakpoints[1]) {
    return "sm";
  }
  if (viewportWidth >= breakpoints[0]) {
    return "xs";
  }

}
