/* 05 - init class method for clarity */
export const initializeClass = (cssClass, jsClass, ...specificArgs) => {
  const extraArg = specificArgs ? specificArgs[0] : undefined;
  if ($(cssClass).length) {
    $(cssClass).each((i, elem) => {
      const item = new jsClass($(elem), extraArg);
      item.init(extraArg);
    });
  }
}
/* 06 - init map method for clarity */
export const initializeMap = () => {
  const
    $locationMapLink = $('.js-show-map'),
    $branchFinder = $('.c-branch-finder'),
    $branchFinderClose = $branchFinder.find('.c-branch-finder__close'),
    loadMap = () => {
      if ($('.c-branch-finder').length) {
        $('.c-branch-finder').each((i, elem) => {
          if (i !== 0) return;
          const branchMap = `branchMap`;
          if (!ADCB.branchMap) {
            dimmerMap(true, true);
            ADCB[branchMap] = new BranchFinder($('.c-branch-finder'));
            ADCB[branchMap].init();
          }
        })
      }
    }

  $locationMapLink.on('click', (e) => {
    e.preventDefault();
    loadMap();
    $branchFinder.addClass('show');
    if (isMobile()) {
      setMapStyles($branchFinder)
    };
  });

  $branchFinderClose.on('click', (e) => {
    e.preventDefault();
    $branchFinder.removeClass('show');
  });
}

export const initializeGrid = (cols = 48) => {
  const grid = new BsGridOverlay(cols);
  grid.init()
}

const setMapStyles = ($branchFinder) => {

  const
    titleHeight = $branchFinder.find('.c-branch-list').outerHeight(),
    windowHeight = $(window).height(),
    stickyHeight = $('.c-branch-finder__nav-mobile').height(),
    $map = $('#gmap'),
    $detailsContainer = $('.c-branch-container'),
    mapHeight = windowHeight - titleHeight - stickyHeight;

  $map.css('margin-top', `${titleHeight}px`);
  $map.css('height', `${mapHeight}px`);
  $detailsContainer.css('height', `${mapHeight}px`);
}

export const isMobile = () => {
  return $(window).width() < 768;
}

export const isRtl = () => {
  return $("html")[0].lang === "ar";
}


export const getOs = () => {

  var userAgent = window.navigator.userAgent,
      platform = window.navigator.platform,
      macosPlatforms = ['Macintosh', 'MacIntel', 'MacPPC', 'Mac68K'],
      windowsPlatforms = ['Win32', 'Win64', 'Windows', 'WinCE'],
      iosPlatforms = ['iPhone', 'iPad', 'iPod'],
      os = null;

  if (macosPlatforms.indexOf(platform) !== -1) {
    os = 'Mac OS';
  } else if (iosPlatforms.indexOf(platform) !== -1) {
    os = 'iOS';
  } else if (windowsPlatforms.indexOf(platform) !== -1) {
    os = 'Windows';
  } else if (/Android/.test(userAgent)) {
    os = 'Android';
  } else if (!os && /Linux/.test(platform)) {
    os = 'Linux';
  }

  return os;

}

export const isTouchDevice = () => {
  const prefixes = ' -webkit- -moz- -o- -ms- '.split(' ');
  const mq = (query) => window.matchMedia(query).matches;

  if (('ontouchstart' in window) || window.DocumentTouch && document instanceof DocumentTouch) {
    return true;
  }

  const query = ['(', prefixes.join('touch-enabled),('), 'heartz', ')'].join('');
  return mq(query);
};

