/* 01 - imports */
import {
  getOs
} from './helpers.js';

/* 02 - export SmsLink class */
export class SmsLink {
  constructor($container) {
    this.$container = $container;
  }

  init() {
    this.modifyHref();
  }

  modifyHref(){
    const osname = getOs();

    if(osname === 'iOS' || osname === 'Mac OS'){
      var hrefdata = this.$container.attr('href');

      if(hrefdata.indexOf('sms:') > -1 && hrefdata.indexOf('body=') > -1){
        var newnewvalue = hrefdata.toString().replace('?body', '&body');
        this.$container.attr('href', newnewvalue);
      }
    }
  }
}
