import { Modal } from "../core/c-modal";

export class BoardCommittee {
  constructor($selector) {
    this.$list = $selector;
    this.isRelated = this.$list.find('.-related').length;
  }
  init() {
    this.instanceCards();
  }
  instanceCards() {
    this.$list.find('.c-offer-card-list__item')
      .each((i,card) => {
        const committeeCard = new BoardCommitteeCard($(card), this.isRelated);
        committeeCard.init();
      })
  }
}

class BoardCommitteeCard {
  constructor($card, isRelated = false) {
    this.$card = $card;
    this.isRelated = isRelated;
    const $modal = $card.find('.c-modal');
    this.modal = new Modal($modal);
    this.modal.init();
  }
  init() {
    this.addEventListeners();
  }
  addEventListeners() {
    //is-anchored is added as a modifier Class inorder to have the Default behavior of the anchor class
    if(!$('.c-offer-cards__list').hasClass('is-anchored')) {
      this.$card
        .off()
        .on('click', e => {
          e.stopPropagation();
          e.preventDefault();
          if(!this.isRelated)
            this.modal.openModal();
        })
    }
  }
}
